.slide-footer
  width: 100%
  height: 2.4em
  position: absolute
  top: 100%
  text-align: center
  line-height: 2.4em
  &.slide-footer--up
    top: auto
    bottom: calc(100% - 1.2em)

  .slide-footer-prompt, .slide-footer-touched, .slide-footer-narration
    width: 100%
    height: 2.4em
    box-sizing: border-box
    position: absolute
    visibility: hidden
    transition: opacity 300ms
    pointer-events: none

  .slide-footer-prompt
    .state-prompt &, .state-intro &
      visibility: visible
      pointer-events: all

  .slide-footer-touched
    .state-touched &
      visibility: visible
      pointer-events: all

  .slide-footer-narration
    .state-narration &
      visibility: visible
      pointer-events: all

  .slide-footer-prompt-content
    line-height: 1

  .btn-muted
    min-width: 8em

  .narration-progress
    box-sizing: border-box
    position: relative
    border: 1px solid rgba(0, 0, 0, 0.25)
    border-left-width: 4px
    background: transparent

  .narration-progress-bar
    position: absolute
    top: 0
    right: 0
    bottom: 0
    left: 0
    background: rgba(0, 0, 0, 0.25)
    transition: transform 350ms linear
    transform: translate3d(0, 0, 0) scale(0, 1)
    transform-origin: left center

  .wave-ellipse
    margin: -1px 0
    position: relative
    top: -4px
    z-index: 1
