@use '../core' as *
@use '../components/typography' as *
.btn
  display: inline-block
  border-radius: 4px
  border: none
  line-height: 1
  box-sizing: border-box
  text-decoration: none
  cursor: pointer
  text-align: center
  max-width: 100%
  @extend .text-bold
  @extend .text-md

  &.__active
    transition: all 300ms !important

  &.btn-block
    display: block

  &.btn-primary
    padding: 0.6em
    color: white
    background: $primary-blue

  &.btn-outlined
    padding: 0.6em
    color: white
    border: 1px solid white
    background: rgba(black, 0.25)

  &.btn-muted
    color: inherit
    padding: 0.6em
    color: #fff
    background: rgba(0, 0, 0, 0.25)

    &.__active
      background: rgba(0, 0, 0, 0.4)

  &.btn-circular
    min-width: 2.5em
    height: 2.5em
    line-height: 2.5em
    border-radius: 1.25em
    padding: 0

    .btn-circular-icon
      width: 1.5em

  &.btn-solid
    color: #333
    padding: 0.8em
    border: 1px solid rgba(0, 0, 0, 0.2)
    background:
      color: #fafafa
      clip: padding-box

    @media (max-width: $screen-sm-max)
      padding: 0.8em 0.75em 0.7em

    &.__active
      border-color: rgba(0, 0, 0, 0.3)
      background-color: #ddd

  &.btn-sm
    padding: 0.55em 1em 0.45em

.btn-knob
  width: 1.6em
  height: 1.6em
  box-sizing: border-box
  color: #fff
  border: 1px solid rgba(0, 0, 0, 0.1)
  border-radius: 50%
  background:
    color: #fff
    clip: padding-box

.draggy,
.selectable
  .btn
    position: relative

    &:before,
    &:after
      top: 50%
      color: #ccc
      height: 1em
      font-size: 1.25em
      line-height: 1.08
      text-shadow: none
      font-family: icons
      position: absolute
      margin-top: -0.5em
      transition: inherit
      font-weight: normal

    .direction-ltr &
      padding-left: 2em
      &:before,
      &:after
        left: 0.5em

    .direction-rtl &
      padding-right: 2em

      &:before,
      &:after
        right: 0.5em

    &.__active:before
      color: #aaa

.draggy
  cursor: move

  .btn
    &:before
      content: "\F131"

  &.draggy-ver
    .btn:before,
    .btn:after
      transform: rotate(90deg)

  &.active
    .btn
      background-color: #e5e5e5

      &:before
        color: #aaa

.selectable
  .btn
    &:before,
    &:after
      font-size: 1.8em
      margin-left: -0.3em
      margin-right: -0.3em

    &:before
      content: "\F111"
      color: #ddd

    &:after
      content: "\F132"
      color: transparent
      transform: translate3d(0, 0, 0) scale(0)
      transition: all 450ms $bounce-md

  &.active
    .btn
      background-color: #e5e5e5

      &:before
        color: #aaa

      &:after
        color: #fff
        transform: translate3d(0, 0, 0) scale(1)

.selectable,
.draggy
  .btn-long
    .direction-ltr &
      padding-left: 2.5em
      text-align: left

    .direction-rtl &
      text-align: right
      padding-right: 2.5em

#done-btn, #prompt-btn
  border: none

#slide-answer-continue
  width: 100%
