// CSS Variables

$bounce-sm: cubic-bezier(.65,.26,.56,1.23)
$bounce-md: cubic-bezier(0.8, 0.5, 0, 1.8)
$bounce-lg: cubic-bezier(1, 1.5, 0, 2.2)

$font-name: "Noto Sans"

$lesson-header-height: 2.4em

// Media breakpoints
$screen-xs-min: 361px
$screen-xs-max: $screen-xs-min - 1px

$screen-sm-min: 601px
$screen-sm-max: $screen-sm-min - 1px

$screen-md-min: 769px
$screen-md-max: $screen-md-min - 1px

$screen-lg-min: 1024px
$screen-lg-max: $screen-lg-min - 1px

// This variable is used in rio/.../CustomDimensionField.tsx
$screen-min-height: 449px

// Block padding
$block-default-xl: 2.0em
$block-default-lg: 1.2em
$block-default-md: 0.6em
$block-default-sm: 0.3em

$block-large-xl: 3.0em
$block-large-lg: 1.8em
$block-large-md: 1.2em
$block-large-sm: 0.4em

$block-landscape-xl: 1.2em
$block-landscape-lg: 0.8em

// Mixins for very large or very small screens
=screen-lg
  @media (min-width: $screen-sm-min) and (min-height: 768px)
    @content

=screen-sm
  @media (max-width: $screen-sm-max), (max-height: $screen-min-height)
    @content

=screen-landscape
  @media (max-height: $screen-min-height)
    @content

=device-iphonex
  @media (width: 375px) and (height: 812px)
    @content

// Grid
$grid-columns: 12

//colors 
$primary-blue: rgba(71, 180, 233, 1)
