@use '../core' as *
.quote
  padding: 1em 1.2em
  position: relative
  transition: all 300ms
  min-width: 20px
  margin-bottom: 1em
  border-radius: 0.6em
  z-index: 1

  &.quote-dk
    color: #fff

    .quote-bg
      background: #000
      opacity: 0.6

    .quote-angle
      fill: #000

  &.quote-lt
    color: #222

    .quote-bg
      background: #fff
      opacity: 0.8

    .quote-angle
      fill: #fff

  &.active
    color: #333
    transition: all 300ms, transform 400ms $bounce-lg

    .quote-bg,
    .quote-angle
      opacity: 1

    .quote-angle
      transform: translate3d(0, -0.5px, 0) scale(1, 0.9)

.quote-bg
  position: absolute
  left: 0
  right: 0
  top: 0
  bottom: 0
  border-radius: inherit
  z-index: -1
  transition: inherit

.quote-angle
  position: absolute
  top: 100%
  transition: inherit
  width: 2em
  height: 1em
  transform-origin: top center
  transform: translate3d(0, 0, 0) scale(1)

.quote-angle-side
  display: none

.direction-ltr .quote.pull-right,
.direction-rtl .quote.pull-left
  .quote-angle
    right: 10%

  .quote-angle-below-right
    display: block

.direction-ltr .quote.pull-left,
.direction-rtl .quote.pull-right
  .quote-angle
    left: 10%

  .quote-angle-below-left
    display: block
