@use '../core' as *

#slides-view
  position: relative
  height: 100%
  overflow: hidden
  text-shadow: 0 1px 0 rgba(#000, 0.05)
  transition: background-color 300ms
  background-color: #f6f6f6

  &.text-dark
    text-shadow: 0 1px 0 rgba(#fff, 0.05)

  @media (min-width: 1025px), (min-height: 769px) and (orientation: landscape)
    border-radius: 3px
    box-shadow: 0 1px 4px #000

  &.direction-rtl
    direction: rtl

    #slides
      direction: ltr
    .slide
      direction: rtl

#slides-view-inner
  padding-top: 4px

  .device-ios &
    padding-top: calc(constant(safe-area-inset-top) + 4px)
    padding-top: calc(env(safe-area-inset-top) + 4px)
    padding-bottom: constant(safe-area-inset-bottom)
    padding-bottom: env(safe-area-inset-bottom)

#slides-background
  position: absolute
  top: 0
  left: 0
  right: -50%
  bottom: 0
  pointer-events: none
  display: block !important
  z-index: 1
  background:
    position: center
    repeat: no-repeat
    size: cover

#slides-lightbox-bg
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  z-index: 1
  transition: background-color 450ms

#lesson-slides
  padding-top:    ($lesson-header-height + 1 * $block-default-lg)
  padding-bottom: ($lesson-header-height + 2 * $block-default-lg)
  position: relative
  z-index: 2

  +screen-lg
    padding-top:    ($lesson-header-height + 1 * $block-large-lg)
    padding-bottom: ($lesson-header-height + 2 * $block-large-lg)

  +screen-landscape
    padding-top:    ($lesson-header-height + 1 * $block-landscape-lg)
    padding-bottom: ($lesson-header-height + 2 * $block-landscape-lg)

.full-screen
  top:    -($lesson-header-height + 1 * $block-default-lg)
  bottom: -($lesson-header-height + 2 * $block-default-lg)
  height: auto !important
  position: absolute
  left:  -$block-default-lg
  right: -$block-default-lg

  +screen-lg
    top:    -($lesson-header-height + 1 * $block-large-lg)
    bottom: -($lesson-header-height + 2 * $block-large-lg)
    left:  -$block-large-lg
    right: -$block-large-lg

  +screen-landscape
    top:    -($lesson-header-height + 1 * $block-landscape-lg)
    bottom: -($lesson-header-height + 2 * $block-landscape-lg)
    left:  -$block-landscape-lg
    right: -$block-landscape-lg

#slides
  margin: -1px 0
  border-top: 1px solid transparent
  border-bottom: 1px solid currentColor
  position: relative
  box-sizing: content-box
  z-index: 1
  transition: border-color 300ms

  .scroll-middle &,
  .scroll-bottom &
    border-top-color: currentColor

  .ui-minimal &
    border-color: transparent

#slides-scroller
  position: relative

.slide
  height: 100%
  position: absolute
  opacity: 0
  transition: opacity 300ms
  z-index: -1

  &.visible
    opacity: 1
    z-index: 0

  .slide-loader
    z-index: -1
    width: 32px
    height: 32px
    padding: 24px
    border-radius: 50%
    background: rgba(#1a1a1a, 0.6) url(assets/img/templates/loader.gif) no-repeat center
    background-size: 32px
    position: absolute
    transition: opacity 300ms

  &.state-complete,
  &.game-paused,
  &.game-count-down
    pointer-events: none

+screen-landscape
  #slides:after
    content: ""
    display: block
    width: 120px
    height: 120px
    position: absolute
    top: 50%
    left: 50%
    margin: -60px
    border-radius: 8px
    background:
      color: rgba(#000, 0.7)
      size: 80px
      repeat: no-repeat
      position: center
      image: url("assets/img/expand-icon.png")

  .slide.visible
    opacity: 0

  .ui-video-active,
  .ui-fullscreen,
  .ui-text-input,
  .ui-flexible-height
    #slides:after
      content: none

    .slide.visible
      opacity: 1

  .device-ios,
  .device-android
    #slides:after
      background-image: url("assets/img/rotate-device.png")
