@use '../core' as *
@use '../components/typography' as *
.game-slide
  .game-state,
  .game-stepper,
  .game-timer,
  .game-score,
  .game-prompt,
  .game-bonus-time,
  .game-progress,
  .content-intro,
  .content-playing,
  .content-outro
    pointer-events: none

  .game-state,
  .game-stepper,
  .game-bonus-time,
  .game-progress,
  .content-intro,
  .content-playing,
  .content-outro
    position: absolute

  $states: intro playing outro

  .content-intro,
  .content-playing,
  .content-outro
    opacity: 0
    transition: opacity 300ms
    left: 0
    right: 0
    top: 0
    bottom: 0

  @each $state in $states
    &.game-#{$state}
      .content-#{$state}
        opacity: 1
        pointer-events: all

  .game-state
    top: 0
    left: 0
    right: 0
    height: 1.2em
    line-height: 1
    z-index: 5

    &:after
      content: ""
      display: block
      border-bottom: 1px solid

    .direction-rtl &
      .text-right
        text-align: left

  +screen-lg
    .game-state-inner
      padding-top: 1.5em
      padding-bottom: 1.5em
      height: 1.2em

  .slide-footer
    border-top: 1px solid

  .game-stepper
    top: 50%
    left: 50%
    min-width: 1.4em
    padding-left: 0.3em
    padding-right: 0.3em
    height: 2em
    margin: -1em 0
    line-height: 2em
    border-radius: 1em
    font-size: 2.4em
    background-color: rgba(#000, 0.5)
    color: #fff
    z-index: 10
    transition: transform 500ms, opacity 300ms 600ms
    text-align: center
    @extend .text-bold

  .game-timer-muted
    font-style: normal
    opacity: 0.5

  .game-bonus-time
    top: initial
    left: initial
    right: initial
    opacity: 0
    transition: all 1200ms
    height: 32px
    line-height: 32px
    padding: 0 12px
    bottom: 2em
    border-radius: 16px
    color: #fff
    background: rgba(#000, 0.6)
    transition: transform 800ms ease-in-out, opacity 300ms ease-in-out
    z-index: 60

  .game-bonus-time
    left: 2em

  .stars-total
    line-height: 0
    font-size: 0.5em

  .game-progress
    top: -4px
    bottom: 0
    left: 0
    right: 0
    height: 4px
    z-index: 10
    overflow: hidden
    background: #222
    opacity: 0
    transition: opacity 300ms

    .game-progress-bar
      background: #fff
      width: 100%
      height: 100%

  &.game-playing .game-progress
    opacity: 1

  &.active.disable-goto-next
    .goto-next
      pointer-events: none
      opacity: 0
