@use '../../sass/core' as *
@use '../../sass/components/typography' as *

.slide-game-jumble
  .game-box
    padding-top: 3.8em
    display: flex
    flex-direction: column

    +screen-lg
      padding-top: 4.2em

  .jumble-top
    overflow: auto
    height: 100%

  .jumble-top-content
    width: auto

  .letter
    width: 1.2em
    height: 2.1em
    line-height: 2.1em
    font-size: 1.5em
    border-radius: 4px
    opacity: 0
    transform: translate3d(0, 50%, 0)
    background-clip: padding-box
    border: 1px solid
    text-align: center

    @media (min-width: $screen-sm-min)
      font-size: 1.8em
      width: 1.5em

    &.visible
      opacity: 1
      transform: translate3d(0, 0, 0)

  .draggy
    display: inline-block
    padding: 0.3em

    &.hidden
      opacity: 0

    &.active
      .letter
        background: rgba(#ccc, 0.7)

    &.active-pos
      .letter
        border-color: transparent
        background: transparent

      &.is-positioned .letter
        color: #333
        background: rgba(#fff, 0.8)

    .letter
      background: rgba(#aaa, 0.4)
      box-shadow: 0 0 1px transparent
      box-sizing: border-box
      display: inline-block

  .droppy,
  .droppy-complete
    min-width: 1em
    display: inline-block
    padding: 0.1em
    @extend .text-pre

    .letter
      background-color: rgba(#000, 0.1)
      border-color: transparent

  .droppy
    &.active
      .letter
        background-color: rgba(#000, 0.4)

  &.active
    .letter
      transition-delay: 0 !important

  .jumble-image-container
    overflow: hidden
    border-radius: 0.5em
    position: relative
    height: 12em
    max-width: 32em
    margin: auto
    background:
      color: #eee
      image: -webkit-linear-gradient(top, #fff, #eee, #fff)
      image: linear-gradient(from bottom, #fff, #eee, #fff)
      clip: padding-box

    @media (min-width: $screen-sm-min)
      height: 14em

  .jumble-masks
    position: absolute
    top: 0
    left: 0
    right: 0
    bottom: 0
    transition: opacity 300ms
    overflow: hidden
    transform: translate3d(0, 0, 0)
    border-radius: 0.5em

  .jumble-mask
    width: 10%
    height: 16.6667%
    float: left
    background-color: #fff

  .jumble-image
    max-height: 100%
    display: block
    margin: auto

  .jumble-prompt
    @media (min-width: $screen-sm-min)
      position: absolute
      top: 1em
      left: 1em
      width: 6em
      font-size: 1.1em

  .jumble-letters
    padding: 1em 0
    color: #fff
    background: rgba(#111, 0.8)
    position: relative
    border-top: 2px solid #fff

    @media (min-width: $screen-sm-min)
      padding: 2em 0

  &.correct .jumble-masks
    opacity: 0
