@use '../../sass/core' as *
@use '../../sass/components/blocks' as *
@use '../../sass/components/content' as *

.slide-game-image-word-match
  .game-box
    padding-top: 3.8em
    display: flex
    flex-direction: column

    +screen-lg
      padding-top: 4.2em

  .match-top
    flex-grow: 1
    display: flex
    align-items: center
    justify-content: center

  .match-top-content
    width: 100%

  .match-words
    color: #fff
    background: rgba(#111, 0.8)
    left: -1px
    right: -1px
    width: auto
    bottom: 0
    border-top: 2px solid #fff

  .match-image-container
    overflow: hidden
    border-radius: 0.5em
    position: relative
    height: 10em
    max-width: 32em
    margin: auto

    @media (min-width: $screen-sm-min)
      height: 16em
      margin-bottom: 1.5em

  .match-image
    transform: translate3d(0, 0, 0)
    transition: all 300ms 150ms
    height: 100%

  .btn-word
    padding: 0.9em 0.3em
    transition: all 300ms, border-left-color 300ms 600ms
    transform: translate3d(0, 0, 0)

    + .btn-word
      border-top: 1px solid rgba(#fff, 0.4)

    &.__active
      background: rgba(#111, 0.95)
      transition-delay: 0ms !important

  .multi-content-wrapper,
  .multi-content-media
    height: 100%
    box-sizing: border-box
    background: transparent

  .multi-content-image
    max-height: 100%

  .multi-content-text
    @extend %content
    @extend .block-v-md

  .hidden
    .match-image,
    .btn-word
      opacity: 0
      transform: translate3d(0, 100%, 0)

    .btn-word
      border-left-color: transparent
      pointer-events: none

@media (min-width: $screen-md-max)
  .match-words
    width: 100%
    display: flex
  .btn-word
    padding: 2em 0.3em !important
    border-left: 1px solid rgba(#fff, 0.4)
    min-width: 20%
    flex-grow: 1
