@use 'sass:color'
@use '../../sass/components/grid' as *

.slide-tap-in-order
  .slide-parent
    overflow: hidden
  .tappy
    &.active
      animation: pulse 600ms 1

      .number
        background: color.adjust(#000, $alpha: -0.8)

    &.incorrect
      animation: rubber 600ms 1

  .tappy-child
    transition: all 400ms

  .number
    border-radius: 50%
    width: 1.6em
    height: 1.6em
    line-height: 1.6em
    transition: all 400ms
    margin: 0 auto

  .number-container
    margin: -0.8em 1em
    position: absolute
    top: 50%
    padding: 0

  .img
    max-height: 100%
    margin: 0 auto

  &.grid-true
    .number-container
      position: static
      margin: -0.2em 0

    .tappy
      @extend .col-sm-6

    .tappy-child
      padding-bottom: 3em
      margin-bottom: -3.2em

  &.state-complete.correct-true
    .tappy-child
        background: color.adjust(#000, $alpha: -0.8)
