@use '../../sass/core' as *

.slide-matrix
  .content-scroll-inner
    max-width: none

  .content-sm-scroll
    @media (min-width: $screen-sm-min)
      background-color: transparent !important

    @media (max-width: $screen-sm-max)
      padding-top: 27px
      background-clip: content-box

      &:before
        content: ""
        position: absolute
        display: block
        width: 100%
        height: 1px
        position: -webkit-sticky
        top: -27px
        margin: -26px 0 28px
        background-color: inherit
        z-index: 20
        opacity: 0.5

  &.game-count-down
    .selectable .btn
      color: transparent
      text-shadow: none
