.slide-reorder
  .draggy-container.fade-in
    opacity: 0

  .draggies
    box-sizing: border-box
    position: relative

  .draggy
    line-height: 1em
    top: 0
    left: 0
    right: 0

    &.active
      z-index: 1

  &.refresh-true
    .draggy
      transition-duration: 0ms !important

  &.ready
    .draggy-container
      opacity: 1
