#content
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  background: #000

  @media (min-width: 1025px), (min-height: 769px) and (orientation: landscape)
    animation: fade-from-black 3200ms 300ms 1 forwards

#inbound,
#outbound
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  z-index: 1

  @media (min-width: 1064px) and (orientation: landscape), (min-height: 960px) and (orientation: landscape)
    margin: 20px

  @media (min-width: 1064px)
    width: 1024px
    left: 50%
    right: initial
    margin-left: -512px
    margin-right: -512px

  @media (min-width: 1320px)
    width: 1280px
    margin-left: -640px
    margin-right: -640px

  @media (min-height: 960px) and (orientation: landscape)
    height: 920px
    top: 50%
    bottom: initial
    margin-top: -460px
    margin-bottom: -460px

#inbound
  z-index: 10
  opacity: 0

.display #content
  #inbound
    opacity: 1
    transition: opacity 600ms

// Invisible text for screen reader
.screen-reader
  border: 0
  padding: 0
  margin: 0
  position: absolute !important
  height: 1px
  width: 1px
  overflow: hidden
  clip-path: inset(50%)
  white-space: nowrap
