.slide-video
  .video
    position: relative
    padding: 0 !important
    width: 100% !important
    height: 100% !important
    margin: 0
    opacity: 0
    transition: opacity 300ms

  .full-screen
    top: 0
    bottom: 0

  &.video-success
    .video
      opacity: 1

  &.video-error
    .video
      opacity: 0

  .video:focus
    outline: none
