.slide-free-text-survey
  .content-scroll
    height: auto
    max-height: 100%

  .user-input-container
    position: relative

  .user-input
    height: 100%
    width: 100%
    min-height: 10em
    padding-right: 2em
    box-sizing: border-box
    border-radius: 4px
    background: rgba(#fff, 0.2)
    hyphens: auto
    cursor: pointer

  .btn-prompt
    position: absolute
    bottom: 50%
    right: 50%
    border-radius: 50%
    margin: -1.5em
    font-size: 32px
    padding: 0
    line-height: 3
    height: 3em
    width: 3em

  &.has-input-true
    .btn-prompt
      right: 0.3em
      bottom: 0.3em
      font-size: 24px
      line-height: 2
      width: 2em
      height: 2em
      margin: 0
      border-radius: 4px 3px 2px 3px
