.slide-number-sentence
  .arrow
    transition: opacity 300ms
    &.disabled
      opacity: 0.5

  .number-wrap
    display: inline-block
    position: relative
    font-weight: bold
    transition: width 300ms
    direction: ltr

  .number
    position: relative
    top: 0
    right: 0
    white-space: nowrap

  .digit
    display: inline-block
