@use '../../sass/core' as *

.slide-image-gallery
  .img
    display: block
    max-width: 100%
    max-height: 100%
    margin: auto

  &.state-narration
    pointer-events: all

  .carousel
    direction: ltr

  .carousel-item
    float: left
    opacity: 0

    .carousel-item-container
      display: flex
      flex-direction: column
      justify-content: center
      align-items: center
      height: 100%
      width: 100%

      .img-container, .caption
        display: flex
        justify-content: center
        align-items: center

      .caption
        flex-shrink: 0

        .text-center
          width: 100%

    @media (min-width: $screen-lg-min)
      .carousel-item-container
        flex-direction: row
        align-items: flex-start

        .img-container
          height: 100%

    .direction-rtl &
      direction: rtl

  .content-wrapper
    padding-bottom: 12px

  .pagination
    position: absolute
    bottom: 1em
    width: 100%
    text-align: center
    direction: ltr
    display: flex
    justify-content: center
    align-items: center

    .dot
      cursor: pointer
      display: inline-block
      width: 10px
      height: 10px
      border: 1px solid
      border-radius: 50%
      margin: 0 4px
      cursor: pointer
      background-color: transparent
      padding: 0
      color: inherit

      &.active
        width: 0
        height: 0
        border-width: 6px
