.slide-text-sequence

  .steps-container
    overflow: hidden

  .title
    font-size: 2em

  .step
    font-size: 1.25em
    opacity: 0
    transform: translate3d(0, 50%, 0)
    margin: 0 0 0.5em

    &.active
      opacity: 1
      transform: translate3d(0, 0, 0)

  .btn-up,
  .btn-down
    position: absolute
    left: 50%
    margin: 0 -1.3em
    opacity: 0
    transition: all 300ms

  .btn-up
    top: 1em

  .btn-down
    bottom: 1em

    &.active
      opacity: 1

  &.no-transition
    .step
      transition: none
