@use '../../sass/components/blocks' as *


.slide-construct
  .word,
  .word-hidden
    display: inline-block

  .word-hidden
    text-shadow: none
    animation: none
    white-space: nowrap

    &.break
      white-space: normal

    &.active
      .blank:after
        opacity: 0.3

    .blank.active
      @extend .block-dk
      border-radius: 3px 3px 0 0

      &:after
        opacity: 1

    &.disabled
      pointer-events: none

  .letter-group,
  .blank
    display: inline-block

  .blank
    min-width: 0.82em
    line-height: 1.25em
    position: relative
    transition: all 300ms
    margin: 0 1px

    &:after
      content: ""
      position: absolute
      border-bottom: 2px solid
      bottom: -2px
      left: 0
      right: 0
      transition: all 250ms
      transform: translate3d(0, -0.2em, 0)

    .letter-inner
      transition: inherit
      color: transparent

    &.visible
      background: transparent !important
      .letter-inner
        color: inherit

    &.visible,
    &.active
      &:after
        transform: translate3d(0, 0, 0)
        opacity: 0.3

  .hyphen
    width: 0
    display: inline-block

  .statement
    line-height: 1.6em

  #content-spacer
    margin-top: 9em

  .statement.text-lg .word
    padding: 0.15em

  .word-letters
    text-align: center
    opacity: 0
    transition: all 300ms
    position: absolute
    left: 0
    right: 0
    bottom: 0
    pointer-events: none

    &.active
      opacity: 1
      pointer-events: all

  .letter
    margin: 0.2em
    width: 2.4em
    padding-left: 0.5em
    padding-right: 0.5em
    text-transform: uppercase
    transition: all 300ms
    transform: scale3d(1, 1, 1)

    &.disabled
      opacity: 0
      transform: scale3d(0, 0, 0)

    &.incorrect
      animation: rubber 1 300ms

  &.game-playing
    .letter
      opacity: 1
      pointer-events: all

  &.state-complete
    .blank
      background: #fff
      border-radius: 3px
      box-shadow: inset 0 0 0 1px

      &:after
        opacity: 0

      .letter-inner
        color: #000
        color: rgba(#000, 0.8)

      &.visible
        box-shadow: none

        .letter-inner
          color: inherit
        &:after
          opacity: 1
