@use '../core' as *
#lesson-menu
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%
  background:
    position: center
    repeat: no-repeat
    size: cover
  z-index: -1
  opacity: 0
  transition: opacity 300ms, z-index 0ms 300ms

  h1, h3
    opacity: 0
    transition: all 0ms 300ms
    transform: translate3d(0, 8px, 0) scale(1)

  #lesson-menu-content
    max-width: 480px
    margin: auto

  .btn
    padding: 1.1em 0.8em
    font-size: 1.4em

    + .btn
      margin-top: 12px

  &.active
    opacity: 1
    z-index: 100
    transition-delay: 0ms, 0ms

    h1, h3
      transition-duration: 400ms
      transition-delay: 0ms
      opacity: 1
      transform: translate3d(0, 0, 0) scale(1)

  #lesson-menu-cancel.__active
    opacity: 0.7

  +screen-landscape
    #lesson-menu-content
      max-width: none

    .btn
      font-size: 1.2em
      display: inline-block
      min-width: 30%
      margin-top: 0 !important
      margin: 0.2em

    #lesson-menu-cancel
      display: block
      margin: 0

  &.page-0
    #lesson-menu-go-back,
    #lesson-menu-restart
      opacity: 0.5
      pointer-events: none
