@use '../../sass/components/blocks' as *
@use '../../sass/components/content' as *

.slide-scratch-to-reveal
  .slider
    position: relative
    margin: auto
    max-width: 100%
    direction: ltr

  .slider-item
    position: absolute
    top: 0
    left: 1px
    right: 1px
    overflow: hidden
    height: 100%

    .direction-rtl &
      direction: rtl

  .btn-replay
    position: absolute
    right: 0.5em
    bottom: 0.5em
    padding: 0.3em
    font-size: 24px
    z-index: 10
    pointer-events: none
    opacity: 0
    transition: opacity 300ms

  .icon
    height: 1em
    display: block

  .multi-content-media
    min-height: 100%
    position: relative

  .slider-item-0
    z-index: 10
    transition: opacity 300ms

    .multi-content-wrapper
      background: transparent

    .multi-content-image
      visibility: hidden

    .multi-content-media
      border-radius: 4px
      box-shadow: 0 1px 3px rgba(#000, 0.2)

  .slider-item-1
    .multi-content-image
      position: absolute
      top: 50%
      left: 50%
      transform: translate3d(-50%, -50%, 0)

  .slider-labels
    position: relative
    overflow: auto

  .slider-label
    position: absolute
    top: 0
    opacity: 0
    width: 100%
    transition: opacity 300ms

  .multi-content-wrapper
    width: 100%
    @extend .block-box-sizing

  .multi-content-text
    @extend .block-lg
    @extend %content-ver

  @media (min-width: 1025px), (min-height: 769px) and (orientation: landscape)
    img.multi-content
      max-height: 240px

  &.state-touched
    .slider-item-0
      opacity: 0
      transition-duration: 800ms

    .btn-replay
      opacity: 1
      pointer-events: all

  &.sized-true
    .multi-content-wrapper
      height: 100%

    .multi-content-media
      @extend %content-ver

  &.state-prompt .slider-label-before,
  &.state-touched .slider-label-after
    opacity: 1
