@use '../../sass/components/blocks' as *
@use '../../sass/components/content' as *

.slide-table
  .table-cell
    padding: 6px

  .table-row:first-child
    .table-cell:first-child
      border-top-left-radius: 4px
    .table-cell:last-child
      border-top-right-radius: 4px

  .table-row:last-child
    .table-cell:first-child
      border-bottom-left-radius: 4px
    .table-cell:last-child
      border-bottom-right-radius: 4px

  .table
    margin: auto
    border-spacing: 2px

    &.table-condensed
      border-spacing: 1px
      .table-cell
        padding: 4px 3px

    &.table-full-width
      width: 100%
      margin: 0 -2px

  .table-row
    @extend .block-lt

    &:nth-child(odd)
      @extend .block-dk

  &.scroll-true
    .table
      width: 100%
      margin: 0 -2px

    .table-wrapper
      padding-bottom: 12px
      margin-bottom: -12px
      @extend %content-scroll

    .table-container
      position: relative
      overflow: hidden

      &:before,
      &:after
        content: ""
        position: absolute
        top: 2px
        bottom: 2px
        width: 6px
        opacity: 0
        transition: opacity 300ms
        background-color: rgba(#000, 0.1)

      &:before
        left: 0

      &:after
        right: 0

    &.active .table-container:before,
    &.active .table-container:after
      opacity: 1

    &.scroll-pos-left .table-container:before,
    &.scroll-pos-right .table-container:after
      opacity: 0
