@use './stars' as *
#stars-available
  padding: 0.6em 1.2em 0.8em
  position: absolute
  top: 4px
  right: 0
  left: 0
  z-index: 10
  background-color: rgba(#333333, 0.25)
  color: #FFFFFF
  text-align: center
  opacity: 0
  transition: opacity 400ms, transform 0ms 400ms
  transform: translate3d(0, -50%, 0)
  pointer-events: none

  .device-ios &
    padding-top: calc(constant(safe-area-inset-top) + 4px)
    padding-top: calc(env(safe-area-inset-top) + 4px)
    top: 0

  .star
    @extend %slide-star
    margin-bottom: 0.5em
    opacity: 0
    transition: all 400ms
    transform: translate3d(0, -100%, 0)

    &.bunched
      margin-top: -0.3em
      margin-bottom: 0.8em

  .display-stars-avail &,   .display-stars-avail & .star
    opacity: 1
    transform: translate3d(0, 0, 0)

  .display-stars-avail &
    transition: all 400ms

    @for $i from 0 through 4
      .star-delay-#{$i}
        transition-delay: 80ms + $i * 120ms
