@use '../core' as *

=make-alignment($type: "")
  .text#{$type}-left
    text-align: left
  .text#{$type}-right
    text-align: right
  .text#{$type}-center
    text-align: center
  .text#{$type}-justify
    text-align: justify
  .text#{$type}-start
    text-align: start
  .text#{$type}-end
    text-align: end

+make-alignment("")

@media (max-width: $screen-md-max)
  +make-alignment("-sm")

@media (min-width: $screen-md-min)
  +make-alignment("-md")

@media (min-width: $screen-lg-min)
  +make-alignment("-lg")
