@use 'sass:color'
@use '../../sass/core' as *

.slide-image-waypoints
  .caption
    padding-bottom: 4em
    position: absolute
    right: 0
    bottom: 0
    left: 0
    background: linear-gradient(to top, #111111 80%, rgba(#111111, 0.6))
    opacity: 0.8
    transition: all 300ms
    transform: translate3d(0, 2em, 0)
    pointer-events: none

    +screen-lg
      padding-bottom: 6em

  .btn-container
    position: absolute
    right: 0
    bottom: 4em
    left: 0
    z-index: 2

    +screen-lg
      bottom: 6em

  .image-container
    margin: auto
    direction: ltr

  .prev-wp, .next-wp
    height: 2.5em
    background-color: color.adjust(#000, $alpha: -0.4)
    background-position: center center
    background-size: 25px 25px
    background-repeat: no-repeat
    line-height: 2.5em
  &.prev-disabled .prev-wp, &.next-disabled .next-wp
    opacity: 0.5
    pointer-events: none

  .direction-rtl &
    .prev-wp .icon:before
      content: "\F127"

    .next-wp .icon:before
      content: "\F128"

  .waypoint, .btn-container
    opacity: 0
    transition: opacity 300ms

  .navigation
    position: absolute
    right: 0
    bottom: -1px
    left: 0
    transition: all 300ms
    pointer-events: none

  .img
    width: 100%
    height: 100%
    object-fit: cover
    display: block
    position: relative
    opacity: 0
    transition: all 300ms
    transform-origin: 0 0

  &.waypoint-white
    .waypoint
      &:before
        background-color: rgba(#000000, 0.2)
      &:hover:before
        background-color: rgba(#000000, 0.4)
      &:after
        border-color: #FFFFFF
        background-color: rgba(#FFFFFF, 0.92)

  &.waypoint-black
    .waypoint
      &:before
        background-color: rgba(#FFFFFF, 0.4)
      &:hover:before
        background-color: rgba(#FFFFFF, 0.6)
      &:after
        border-color: #000000
        background-color: rgba(#000000, 0.8)

  &.guided-false
    .caption
      opacity: 0

    .waypoint
      margin: -1.25em
      width: 2.5em
      height: 2.5em
      position: absolute
      cursor: pointer

      &.tapped
        &:after
          animation: scale-down-75 300ms 1 forwards

      &:after, &:before
        content: ""
        display: block
        position: absolute
        top: 0
        right: 0
        bottom: 0
        left: 0
        border-radius: 50%
        transition: all 300ms
        animation: pulse 1150ms infinite alternate

      &:after
        margin: 4px
        border-width: 1px
        border-style: solid
        animation-delay: -400ms

  &.guided-true
    .caption
      padding-bottom: 8em

      +screen-lg
        padding-bottom: 9em

    .btn-container
      opacity: 1
      pointer-events: all

    .img
      pointer-events: none

  &.caption-true
    .caption
      position: relative
      opacity: 1
      transform: translate3d(0, 0, 0)

  &.is-loaded
    .img, .waypoint
      opacity: 1

  &.zoom-true
    .waypoint
      opacity: 0
      pointer-events: none

  &.no-transition
    .img, .waypoint, .caption, .navigation, .btn-container
      transition: none !important
