@use '../../sass/core' as *

.slide-pie-chart
  .pie
    margin: 0 auto
    width: 18em
    height: 18em
    position: relative

    &:after
      content: ""
      padding: 2em
      width: 100%
      height: 100%
      position: absolute
      top: -2em
      left: -2em

  .pie-ring-svg
    width: 100%
    height: 100%
    position: absolute
    top: 0
    left: 0
    border-radius: 50%

  .pie-ring
    transform-origin: center center

    fill: transparent
    stroke: rgba(0, 0, 0, 0.3)
    stroke-width: 4.5

  .pie-ring-answer
    stroke: transparent

  .pie.active .pie-ring
    transition: none

  .pie-ring-value
    margin: auto
    width: 3em

  &.state-complete
    .pie-ring-input
      stroke: rgba(0, 0, 0, 0.2)

    .pie-ring-answer
      stroke: rgba(0, 0, 0, 0.2)

  @media (max-width: $screen-sm-max)
    .pie
      font-size: 10px

    .pie-label-text
      font-size: 14px
