@use '../../sass/core' as *
@use '../../sass/core/mixin' as *

.slide-chat
  h2
    margin-bottom: 0.25em

  .chat-msg-text,
  .chat-msg-elli
    min-height: 1em

  .chat-msg-elli
    position: absolute
    &.hide
      display: none

  .selectable,
  .chat-msg-text,
  .chat-responder
    opacity: 0

  .selectable:nth-child(3n + 1) .quote-angle
    margin: 0 40px

  .selectable:nth-child(3n + 2) .quote-angle
    margin: 0 20px

  .chat-msg-text,
  .chat-responder
    transition: opacity 300ms

  .chat-responder
    .direction-rtl &
      text-align: left

  &.show-msg-true
    .chat-msg-elli
      display: none

    .chat-msg,
    .chat-msg-text,
    .chat-responder
      opacity: 1

    .selectable
      opacity: 1
      transform: translate3d(0, 0, 0) scale(1)

      &.active
        transition: all 300ms, transform 400ms $bounce-lg

  &.game-count-down
    .selectable
      opacity: 1
      transform: translate3d(0, 0, 0) scale(1)
      color: transparent
      text-shadow: 0 0 transparent

  &.state-complete
    .chat-msg
      transition-delay: 0ms !important

      &.incorrect
        color: #222
        background: rgba(#fff, 0.8)
        opacity: 0.5

        &:after
          border-top-color: rgba(#fff, 0.8)
          border-right-color: rgba(#fff, 0.8)

        &.active
          animation: rubber 600ms 1 forwards

      &.correct
        transform: translate3d(0, 0, 0) scale(1.1)
        background: #fff
        color: #333

        &:after
          border-top-color: #fff
          border-right-color: #fff

  .message
    padding: 12px
    border-radius: 12px 12px 12px 12px
    overflow-wrap: break-word
    clear: both
    max-width: calc(100% - 24px)
    &--answer
      background: rgba(white, .8)
      @include quote-angle(right, 12px, 9px, rgba(white, .8))
    &--question
      background: rgba(black, .6)
      @include quote-angle(left, 12px, 9px, rgba(0,0,0, .6))
