@use '../icons' as *

.pswp__button
  width: 44px
  height: 44px
  line-height: 44px
  position: relative
  background: none
  cursor: pointer
  overflow: visible
  -webkit-appearance: none
  display: block
  border: 0
  padding: 0
  margin: 0
  float: right
  opacity: 0.75
  transition: opacity 0.2s
  box-shadow: none
  color: inherit

  @extend %icon

  &:before
    font-size: 32px

  &:focus,
  &:hover
    opacity: 1

  &:active
    outline: none
    opacity: 0.9

  &::-moz-focus-inner
    padding: 0
    border: 0

.pswp__ui--over-close .pswp__button--close
  opacity: 1

.pswp__button--close:before
  content: "\F11F"

.pswp--supports-fs .pswp__button--fs
  content: "\F108"

.pswp--fs .pswp__button--fs:before
  content: "\F109"

.pswp__button--zoom:before
  content: "\F120"

.pswp--zoomed-in .pswp__button--zoom:before
  content: "\F121"

.pswp__button--arrow--left:before
  content: "\F128"

.pswp__button--arrow--right:before
  content: "\F127"

.pswp__button--fs
  display: none

.pswp__button--zoom
  display: none

.pswp--zoom-allowed .pswp__button--zoom
  display: block

.pswp--touch
  .pswp__button--arrow--left,
  .pswp__button--arrow--right
    visibility: hidden

.pswp__button--arrow--left,
.pswp__button--arrow--right
  top: 50%
  margin-top: -50px
  width: 70px
  height: 100px
  position: absolute

.pswp__button--arrow--left
  left: 0

.pswp__button--arrow--right
  right: 0

.pswp__button--arrow--left:before,
.pswp__button--arrow--right:before
  top: 28px
  background-color: rgba(0, 0, 0, 0.3)
  width: 44px
  position: absolute

.pswp__button--arrow--left:before
  left: 8px

.pswp__button--arrow--right:before
  right: 8px

.pswp__hint,
.pswp__counter
  position: absolute
  height: 44px
  line-height: 44px
  font-weight: bold
  text-transform: uppercase
  letter-spacing: 0.2px
  padding: 0 12px
  will-change: opacity
  transition: opacity 300ms 2000ms

.pswp__hint
  color: rgba(#fff, 0.6)

  &:before
    content: "Swipe down to dismiss"

.pswp__counter
  -moz-user-select: none
  -ms-user-select: none
  user-select: none
  position: absolute
  color: #FFF
  opacity: 0
  transition-duration: 600ms

.pswp__caption
  position: absolute
  left: 0
  bottom: 0
  width: 100%
  min-height: 44px

  .pswp--has_mouse &
    pointer-events: none

  .pswp--has_mouse.pswp--zoomed-in &
    opacity: 0

  small
    font-size: 11px
    color: #BBB

.pswp__caption__center
  text-align: left
  max-width: 420px
  margin: 0 auto
  font-size: 20px
  padding: 44px 16px 24px
  text-align: center

.pswp__caption--empty
  display: none

.pswp__caption--fake
  visibility: hidden

.pswp__preloader
  width: 44px
  height: 44px
  position: absolute
  top: 0
  left: 50%
  margin-left: -22px
  opacity: 0
  transition: opacity 0.25s ease-out
  will-change: opacity
  direction: ltr

.pswp__preloader__icn
  width: 20px
  height: 20px
  margin: 12px

.pswp__preloader--active
  opacity: 1

  .pswp__preloader__icn
    background: url("assets/img/templates/loader.gif") 0 0 no-repeat
    background-size: 100%

@media screen and (max-width: 1024px)
  .pswp__preloader
    position: relative
    left: auto
    top: auto
    margin: 0
    float: right

.pswp__ui
  visibility: visible
  opacity: 1
  z-index: 1550

.pswp__top-bar
  position: absolute
  left: 0
  top: 0
  right: 0
  height: 44px
  padding: 8px

  .device-ios &
    padding-top: 24px

.pswp__caption,
.pswp__top-bar
  will-change: opacity
  transition: opacity 333ms cubic-bezier(0.4, 0, 0.22, 1)

.pswp--notouch
  .pswp__hint:before
    content: "Press ESC to dismiss"

.pswp--has_mouse
  .pswp__button--arrow--left,
  .pswp__button--arrow--right
    will-change: opacity
    transition: opacity 333ms cubic-bezier(0.4, 0, 0.22, 1)

  .pswp__button--arrow--left,
  .pswp__button--arrow--right
    visibility: visible

.pswp__caption
  background: linear-gradient(to bottom, rgba(#000, 0), rgba(#000, 0.95) 120px)

  h1
    font-size: 1.2em
    margin: 0 0 4px
    font-weight: bold

.pswp__ui--idle
  .pswp__top-bar,
  .pswp__button--arrow--left,
  .pswp__button--arrow--right
    opacity: 0

.pswp__ui--hidden
  .pswp__top-bar,
  .pswp__caption,
  .pswp__button--arrow--left,
  .pswp__button--arrow--right
    opacity: 0.001

.pswp__ui--one-slide
  .pswp__button--arrow--left,
  .pswp__button--arrow--right,
  .pswp__counter
    display: none

.pswp__element--disabled
  display: none !important

.pswp--minimal--dark .pswp__top-bar
  background: none
