=backface-visibility($visibility)
  -webkit-backface-visibility: $visibility
  -moz-backface-visibility: $visibility
  backface-visibility: $visibility


@mixin quote-left($direction, $distance-from-corner, $height)
  right: auto
  left: $distance-from-corner
  border-width:  $height $height $height 0

@mixin quote-right($direction, $distance-from-corner, $height)
  left: auto
  right: $distance-from-corner
  border-width:  $height 0 $height $height

=quote-angle($direction, $distance-from-corner, $height, $color)
  &:after
    content: ''
    position: absolute
    top: 100%
    border-style: solid
    border-color: $color transparent transparent


    @if $direction == left
      @include quote-left($direction, $distance-from-corner, $height)
      .direction-rtl &
        @include quote-right($direction, $distance-from-corner, $height)
    @else
      @include quote-right($direction, $distance-from-corner, $height)
      .direction-rtl &
        @include quote-left($direction, $distance-from-corner, $height)

@mixin multi-line-ellipsis($lines)
  display: -webkit-box
  -webkit-line-clamp: $lines
  -webkit-box-orient: vertical
  overflow: hidden
  text-overflow: ellipsis
