.slide-video-collection
  .thumb-content
    position: absolute
    z-index: 1
    left: 50%
    top: 50%
    width: 100%
    max-height: 100%
    transform: translate(-50%, -50%)

  .video-details
    position: absolute
    top: 0
    left: 0
    width: 100%
    opacity: 0
    pointer-events: none

    &.active
      opacity: 1
      pointer-events: all

  .video-container
    top: 0
    bottom: 0

  .video
    position: relative
    padding: 0 !important
    width: 100% !important
    height: 100% !important

  .exit-video
    color: #fff
    position: absolute
    top: -0.8em
    right: -0.8em
    padding: 1.2em
    cursor: pointer

  .tappies-parent
    overflow: hidden
    position: absolute
    left: 0
    right: 0
    transition: opacity 300ms
    z-index: 1

  .tappy
    box-sizing: border-box
    max-height: 160px
    cursor: pointer

    &.visited
      .tappy-child
        transition: all 300ms
        opacity: 0.6
        background-color: rgba(#000, 0.4)

  .tappy-child
    overflow: hidden
    height: 100%
    margin: 0 auto
    float: none
    position: relative
    background-size: cover
    background-position: center center

    &.dark
      color: #fff
      &:before
        content: ''
        position: absolute
        top: 0
        left: 0
        bottom: 0
        right: 0
        background-color: rgba(#000, 0.6)

  .video-js
    background: transparent

  &.video-true
    .video-details
      display: none
      &.active
        display: block

    .tappies-parent
      opacity: 0
      pointer-events: none
