.slide-scrolling-media
  .slide-content
    padding-bottom: 1px

  .multi-content-wrapper
    max-width: 100%
    position: relative

  @media (min-width: 1025px), (min-height: 769px) and (orientation: landscape)
    .video-js video
      max-height: 240px

  &.image-focus-true
    .multi-content-wrapper-image:after
      content: "\f108"
      font-family: icons
      font-size: 1.6em
      position: absolute
      top: 4px
      right: 4px
      line-height: 1.2em
      width: 1.2em
      text-align: center
      color: #fff
      background: rgba(#000, 0.12)
      border-radius: 4px

  @media (min-width: 1025px), (min-height: 769px) and (orientation: landscape)
    img.multi-content
      max-height: 240px

  a
    word-wrap: break-word
