@use '../../sass/core' as *

.slide-image-multiple-choice
  .game-progress
    bottom: 2px

  .tappies
    margin: 0 -8px

    @media (min-width: $screen-sm-min)
      margin: 0 -16px

  .tappy
    display: inline-block
    box-sizing: border-box
    overflow: hidden
    max-width: 480px
    vertical-align: top

    &.active
      animation: pulse 600ms 1
      animation-delay: 0ms !important

      .tappy-selection
        background: rgba(#000, 0.3)

        &:after
          background: white

  .tappy-selection
    border-radius: 50%
    width: 1.5em
    height: 1.5em
    transition: all 400ms
    position: absolute
    right: 0
    bottom: 0
    margin: 0.5em
    background: rgba(#000, 0.06)

    @media (min-width: $screen-lg-min)
      right: 50%
      margin-right: -0.75em

    &:after
      content: ""
      position: absolute
      top: 0
      left: 0
      right: 0
      bottom: 0
      margin: 4px
      transition: all 400ms
      border-radius: 50%

  .tappy-img
    max-height: 100%
    max-width: 100%

  &.state-complete
    .tappy-selection,
    .tappy-selection.active
      background: transparent
    .tappy
      &.correct
        animation: pulse 600ms 0ms 1 !important

        .tappy-content
          background: rgba(#000, 0.2)
          transition-duration: 300ms

      &.incorrect.active
        animation: rubber 600ms 1

  .selectable.show-incorrect
    animation: rubber 600ms 1 forwards !important
