@use '../../sass/core' as *

.slide-game-next-in-order
  .next-img-container
    display: inline-block
    width: 8em

    @media (max-width: $screen-sm-max)
      width: 6em

    &.hidden-next
      background: rgba(#fff, 0.6)

    &.hidden .next-img
      opacity: 0

  .next-img,
  .btn-image
    box-sizing: border-box
    transform: translate3d(0, 0, 0)
    transition: all 300ms
    vertical-align: middle
    max-height: 100%
    max-width: 100%

  .btn-image
    transform: translate3d(0, 0, 0) scale(1) rotate(0deg)

    &.__active
      transform: translate3d(0, 0, 0) scale(0.9) rotate(0deg)
      opacity: 0.9

  .next-top
    position: absolute
    top: 2em
    left: 2em
    right: 2em

    @media (max-width: $screen-sm-max)
      height: calc(100% - 12em)

    @media (min-width: $screen-sm-min)
      height: calc(100% - 16em)

  .next-sequence
    text-align: left
    white-space: nowrap
    transform: translate3d(0, 0, 0)

  .next-options
    position: absolute
    color: #fff
    background: rgba(#111, 0.8)
    left: 0
    right: 0
    width: auto
    bottom: 0
    border-top: 2px solid #fff

  .btn-image-container
    height: 12em
    line-height: 12em
    display: inline-block
    max-width: 180px

    @media (max-width: $screen-sm-max)
      max-width: 60px
      height: 8em
      line-height: 8em

  .hidden
    .next-img
      opacity: 0
      transform: translate3d(0, 100%, 0)
      transition-delay: 400ms

  .hidden,
  .between
    .btn-image
      opacity: 0
      transform: translate3d(0, 100%, 0)
