@use '../../sass/core' as *
@use '../../sass/core/mixin' as *

.slide-speech-bubble
  .content-scroll-inner
    min-height: 100%

  .quote-container
    max-width: 100%
    flex-shrink: 0

  .quote
    margin-bottom: 0
    &.pull-left
      &.quote-dk
        @include quote-angle(left, 22%, 20px, rgba(black, .6))
      &.quote-lt
        @include quote-angle(left, 22%, 20px, rgba(white, .8))
    &.pull-right
      &.quote-dk
        @include quote-angle(right, 22%, 20px, rgba(black, .6))
      &.quote-lt
        @include quote-angle(right, 22%, 20px, rgba(white, .8))

  .speech-right
    .quote-angle-below-right
      display: block

    .author
      justify-content: flex-end

    &.has-avatar
      .quote-angle
        right: 20%

      .author
        padding-right: 20%
        flex-direction: row-reverse
        justify-content: flex-start

  .speech-left
    .quote-angle-below-left
      display: block

    &.has-avatar
      .quote-angle
        left: 20%

      .author
        padding-left: 20%

  .author-avatar
    margin: 0 0 0 -2em
    width: 4em
    height: 4em
    object-fit: cover

    flex-shrink: 0


  .speech-style-circle
    .author-avatar
      border-radius: 50%

  .speech-style-rounded
    .author-avatar
      border-radius: 4px

  .author
    padding: 2em 1em 0
    align-items: center
    &.author-right
      .author-avatar
        margin: 0 -2em 0 0
      .author-content
        margin: 0 15px 0 3em

  .has-avatar
    .author
      padding: 1.5em 0 0
      min-height: 4em

    .author-content
      margin: 0 3em 0 15px
