#pagination
  position: absolute
  bottom: 0
  left: 0
  right: 0
  line-height: 0
  text-align: center

  .page
    display: inline-block
    border: 1px solid #ccc
    height: 8px
    width: 8px
    margin: 0 4px 12px
    border-radius: 50%
    transition: background-color 300ms

    &.active
      background: #ccc
