@use '../core' as *
=make-block($size, $padding)
  .block-h-#{$size}
    padding-left: $padding
    padding-right: $padding

  .block-v-#{$size}
    padding-top: $padding
    padding-bottom: $padding

  .block-#{$size}
    padding: $padding

.block-box-sizing
  box-sizing: border-box

.block-frame
  position: relative

.block-flex-ver
  height: 100%
  display: flex
  align-items: center

  .block-flex-child
    width: 100%

.block-flex-hor
  width: 100%
  display: flex
  justify-content: center

.block-flex-center
  display: flex
  align-items: center
  justify-content: center

.block-flex-child
  -webkit-box-flex: 1
  -moz-box-flex: 1
  -ms-box-flex: 1
  -webkit-box-ordinal-group: 1
  -moz-box-ordinal-group: 1
  -ms-flex-order: 1
  -webkit-order: 1
  order: 1
  flex-grow: 1

.direction-column
  flex-direction: column

.display-flex
  display: flex

.block-inline
  display: inline-block

=make-block-variant($name, $color)
  .block-#{$name}
    border-radius: 4px
    background: $color

    .block-lt,
    .block-dk
      border-radius: 2px

+make-block(xl, $block-default-xl)
+make-block(lg, $block-default-lg)
+make-block(md, $block-default-md)
+make-block(sm, $block-default-sm)

+screen-lg
  +make-block(xl, $block-large-xl)
  +make-block(lg, $block-large-lg)
  +make-block(md, $block-large-md)
  +make-block(sm, $block-large-sm)

+screen-landscape
  +make-block(xl, $block-landscape-xl)
  +make-block(lg, $block-landscape-lg)

+make-block-variant(dk, rgba(#000, 0.08))
+make-block-variant(lt, rgba(#fff, 0.14))
