@use "sass:math"
@use '../core' as *

#slide-answer
  bottom: -20px
  left: 0
  right: 0
  position: absolute
  border-bottom: 20px solid #fff
  border-top: 4px solid rgba(0, 0, 0, 0.2)
  margin-top: -1px
  line-height: 1.33
  color: #222
  z-index: 99
  transform: translate3d(0, 100%, 0)
  transition: transform 300ms
  background:
    color: #fff
    image: linear-gradient(to bottom, rgba(#fff, 0.93), #fff 4em)
    clip: padding-box

  .device-ios &
    padding-bottom: calc(constant(safe-area-inset-bottom))
    padding-bottom: calc(env(safe-area-inset-bottom))

  #slide-answer-details
    position: relative

    @media (max-width: $screen-md-min)
      &:before,
      &:after
        content: ""
        position: absolute
        left: $block-large-lg * 0.5
        right: $block-large-lg * 0.5
        z-index: 1

      &:before
        top: 0
        height: $block-default-lg
        background: linear-gradient(to bottom, #fff, rgba(#fff, 0))

      &:after
        bottom: -1px
        height: $block-default-lg * 1.5
        background: linear-gradient(to top, #fff, rgba(#fff, 0.95) 10%, rgba(#fff, 0))

    .content-scroll
      padding-left: $block-large-lg * 0.5
      padding-right: $block-large-lg * 0.5
      margin: 0 math.div($block-large-lg, -2)
      height: auto
      max-height: 335px

      @media (max-width: $screen-md-min)
        max-height: 250px
      +screen-landscape
        max-height: 180px
        max-width: 83.33%
        margin: auto

  #slide-answer-takeaway
    padding-bottom: $block-default-lg
    max-height: 250px
    overflow-y: scroll

    @media (max-width: $screen-md-min)
        max-height: 205px

  @media (max-width: $screen-md-max)
    .col-sm-12 + .col-sm-12
      &:before
        content: ""
        left: 25%
        right: 25%
        position: absolute
        border-bottom: 1px solid #ccc
        top: 0
        z-index: 10

  @media (min-width: $screen-md-min)
    #slide-answer-continue-container
      position: relative
      &:before
        content: ""
        left: 25%
        right: 25%
        position: absolute
        border-bottom: 1px solid #ccc
        top: -$block-default-lg
        z-index: 10

  +screen-lg
    #slide-answer-continue-container:before
      top: -$block-large-lg

  #slide-answer-takeaway + #slide-answer-continue-container
    @media (min-width: $screen-md-min)
      padding-top: $block-default-lg
      &:before
        content: ""
        left: 25%
        right: 25%
        position: absolute
        border-bottom: 1px solid #ccc
        top: 0

    #slide-answer-continue
      max-width: 360px
      margin: auto

  &.display
    transform: translate3d(0, 0, 0)
    transition-delay: 600ms

  #slide-answer-container
    width: 100%
    max-height: 580px
    display: flex
    flex-direction: column
    @media (min-width: $screen-md-min)
      flex-direction: row
      align-items: center

      &.split-column
        flex-direction: column

  #block-container
    display: flex
    justify-content: center
