@use '../../sass/components/blocks' as *
@use '../../sass/components/content' as *

.slide-comparison
  .slider-wrapper
    position: relative
    z-index: 10

  .slider
    position: relative
    margin: 1.2em 10px
    max-width: 100%
    direction: ltr
    cursor: ew-resize

  .slider-item
    position: absolute
    top: 0
    left: -1px
    right: -1px
    overflow: hidden
    height: 100%
    pointer-events: none

    .direction-rtl &
      direction: rtl

  .slider-labels
    position: relative
    opacity: 0
    transform: translate3d(0, 3em, 0)

  .slider-label
    position: absolute
    top: 0
    opacity: 0
    width: 100%
    transition: opacity 300ms

  .multi-content-text
    border-radius: 3px

  .slider-item-0 .block-lt
    background: linear-gradient(to right, rgba(#fff, 0.25), rgba(#fff, 0.04))

  .slider-item-1 .block-lt
    background: linear-gradient(to left, rgba(#fff, 0.25), rgba(#fff, 0.04))

  .slider-item-0 .multi-content-text
    background: linear-gradient(to right, rgba(#000, 0.12), rgba(#000, 0.06))

  .slider-item-1 .multi-content-text
    background: linear-gradient(to left, rgba(#000, 0.12), rgba(#000, 0.06))

  .text-dark &
    .slider-item-0 .block-lt
      background: linear-gradient(to right, rgba(#000, 0.12), rgba(#000, 0.06))

    .slider-item-1 .block-lt
      background: linear-gradient(to left, rgba(#000, 0.12), rgba(#000, 0.06))

  .multi-content-wrapper
    width: 100%
    @extend .block-box-sizing

  .multi-content-text
    @extend .block-lg
    @extend %content-ver

    :last-child
      margin-bottom: 0

  @media (min-width: 1025px), (min-height: 769px) and (orientation: landscape)
    img.multi-content
      max-height: 14em

  .slider-knob
    height: 100%
    width: 2px
    background: rgba(#000, 0.4)
    position: absolute
    z-index: 10
    margin-left: -1px

    &:before,
    &:after
      content: ""
      display: block
      margin: -5px -10px
      width: 20px
      height: 20px
      background: #fff
      border-radius: 50%
      position: absolute
      border: 1px solid rgba(0, 0, 0, 0.2)
      background-clip: padding-box

    &:after
      bottom: 0

  &.sized-true
    .multi-content-wrapper
      height: 100%

    .multi-content-media
      @extend %content-ver

  &.side-before .slider-label-before,
  &.side-after .slider-label-after
    opacity: 1

  & .initialized.slider-labels
      opacity: 1
      transition: all 300ms 300ms
      transform: translate3d(0, 0, 0)
