.slide-url
  .url-object
    position: absolute
    width: 100%
    height: 100%
    left: 0
    right: 0
    top: 0
    bottom: 0

  .exit-btn
    position: absolute
    left: 0
    right: 0
    z-index: 1
    font-size: 18px
    font-weight: bold
    padding: 0
    min-height: 48px
    padding: 0.8em 0.5em
    text-align: center
    color: #fff
    background: rgba(#222, 0.8)
    border: none
    display: block
    margin: 0
    border-radius: 0
    transition: all 300ms
    box-sizing: border-box
    -webkit-appearance: none

    &.__active
      background: rgba(#222, 0.9)

  &.exit-btn-overlay-top
    .exit-btn
      top: 0

  &.exit-btn-overlay-bottom,
  &.exit-btn-below
    .exit-btn
      bottom: 0

  &.exit-btn-overlay-top,
  &.exit-btn-overlay-bottom
    .exit-btn
      margin: 0.5em
      right: auto
      width: 300px
      left: 50%
      margin-left: -150px
      border-radius: 3px
