@use 'sass:color'
#social-view
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  z-index: -1
  text-shadow: none
  opacity: 0
  background: rgba(#000, 0.2)
  transition: all 300ms, z-index 0ms 300ms
  border-radius: 3px

  &.is-input-mode
    background: #fff

  .time
    clear: both

  .comment + .time
    padding-top: 1em

  .emoji
    font-size: 3em
    line-height: 1.25
    margin-bottom: -0.25em
    display: block

  .comment
    max-width: 85%
    min-width: 4em
    clear: both
    padding-top: 0.5em

    &:nth-child(n-4)
      opacity: 0
      transform: translate3d(0, 20px, 0)
      transition: all 400ms

    &.is-selected
      .comment-message
        box-shadow: inset 0 0 0 1px #ccc
        background: #fff

        &:after
          border-color: #ccc transparent transparent #ccc

        &:before
          content: ""
          opacity: 1

      .comment-toggle-action
        position: relative
        opacity: 1
        pointer-events: all

    &.is-learner
      .comment-message
        color: #fff
        background-color: #46b4e9
        background-image: linear-gradient(to right, #45b1e6, #44c3ed)
        border-radius: 12px 12px 0 12px
        box-shadow: none

        &:before
          content: none

        &:after
          margin-left: auto
          border-color: #44c3ed #44c3ed transparent transparent

      &.is-selected .comment-message
        background-image: linear-gradient(to bottom right, color.adjust(#46b4e9, $lightness: -12%), #44c3ed)

      .comment-label
        text-align: right

    &.is-learner-liked
      .comment-like .icon
        color: #46b4e9

    &.is-learner-flagged
      .comment-message
        opacity: 0.5

    &.no-likes
      .comment-like
        color: #b4bcc6

  .time + .comment.is-learner
    margin-top: -1.62em

  .comment,
  .time
    margin: 0

  .comment-message
    clear: both
    border-radius: 12px 12px 12px 0
    background: #f2f4f6
    hyphens: auto
    transition: opacity 300ms
    white-space: pre-wrap
    cursor: pointer
    padding: 12px

    &:before,
    &:after
      display: block
      width: 0px
      height: 0
      border: solid transparent
      margin: 12px -12px -16px
      border-top-color: #f2f4f6
      border-left-color: #f2f4f6
      border-width: 2px 6px

    &:before
      opacity: 0
      bottom: -3px
      left: 1px
      margin: 0
      position: absolute
      border-color: #fff transparent transparent #fff
      transform: translateY(-0.4px)

    &:after
      content: ""

  .comment-label,
  .comment-toggle-action
    height: 1.8em

  .comment-action
    display: inline-block
    padding: 12px 1em
    margin: -12px -1em
    cursor: pointer

    .icon:before
      vertical-align: text-bottom

  .comment-toggle-action
    margin-top: -1.8em
    background: rgba(#fff, 0.96)
    background: linear-gradient(to right, rgba(#fff, 0), rgba(#fff, 0.96) 32px)
    opacity: 0
    pointer-events: none
    transition: all 300ms
    margin-left: -32px
    padding-left: 32px

  .comment-cancel
    float: right

    &:before
      vertical-align: middle

  .loader
    display: block
    opacity: 0
    margin: auto
    transition: all 300ms

  &.is-loading
    .loader
      opacity: 1

  &.visible
    z-index: 100
    opacity: 1
    transition: all 300ms

  &.active
    #text-input-scroll,
    .text-input-container
      opacity: 1

    #social-view-messages,
    .comment
      opacity: 1
      transform: translate3d(0, 0, 0)

#social-view-no-comments
  height: 1em
  position: absolute
  margin-top: -0.5em
  top: 50%
  left: 0
  right: 0

#social-view-messages
  position: absolute
  left: 0
  right: 0
  bottom: 0
  background: #fff
  color: #000
  transform: translate3d(0, 100%, 0)
  transition: transform 300ms

  &:before,
  &:after
    content: ""
    position: absolute
    pointer-events: none
    left: 0
    right: 0

  &:before
    top: -3px
    background: rgba(#000, 0.2)
    height: 3px

  .is-input-mode &
    &:before,
    &:after
      height: 60px
      z-index: 10

    &:before
      top: 0
      right: 8px
      background: linear-gradient(#fff, rgba(#fff, 0))

    &:after
      bottom: 0
      background: linear-gradient(transparent, rgba(#000, 0.03))

#social-view-messages-list
  box-sizing: border-box
  overflow: auto
  position: relative
  min-height: 6em
  max-height: 48vh

  ul
    list-style: none
    padding: 0

  .is-input-mode &
    height: 100%
    max-height: none
    padding-top: 3.2em

#social-view-input-start
  border-top: 1px solid rgba(#000, 0.08)
  margin-top: -1px
  z-index: 1
  position: relative
  .is-input-mode &
    display: none

#social-view-messages
  .is-input-mode &
    top: 0
    height: 60%

#social-view-input
  position: absolute
  top: 60%
  left: 0
  right: 0
  bottom: 0
  background: #fff

#social-view-input-cancel
  position: absolute
  top: 0
  right: 0
  padding: 12px
  z-index: 10
  height: 1em
  font-size: 24px
  color: #9ea6b1
  background: radial-gradient(rgba(#fff, 0.4) 20%, rgba(#fff, 0) 60%) no-repeat
  background-size: 48px
  cursor: pointer

#social-view-input-title,
#social-view-input-slide-count,
#social-view-input-limit
  vertical-align: middle
  padding: 0.6em 0.8em 0.6em 0
  height: 2.4em
  line-height: 1.4em
  box-sizing: border-box

#social-view-input-slide-count,
#social-view-input-limit
  color: #9ea6b1
  text-align: right
  display: inline-block

#social-view-input,
#social-view-input-cancel
  display: none
  .is-input-mode &
    display: block

#social-view-input-limit
  display: none
  .has-limit &
    display: inline-block

  .over-limit &
    color: #fd5143

#social-view-input-text
  display: block
  resize: none
  width: 100%
  height: 100%
  border: none
  padding-bottom: 1.5em
  border-bottom: 1.5em solid #fff
  box-sizing: border-box
  outline: none

#social-view-input-toolbar
  position: absolute
  left: 0
  right: 0
  bottom: 0
  color: black
  pointer-events: none
  background: linear-gradient(rgba(#fff, 0), #fff 50%)

#social-view-input-confirm
  pointer-events: all
  margin: 0
  min-width: 100px

  .over-limit &
    pointer-events: none
    color: #aaa

.is-ios
  &#social-view .emoji
    line-height: 1.1
    margin-bottom: -0.1em
