.slide-connect
  .connect-canvas
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%

  .connect-canvas-container
    z-index: 0

  .connect-boxes
    pointer-events: none

  img.multi-content
    max-height: 100%

  .connect-box
    transition: none
    box-sizing: border-box
    height: 100%
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center

    .multi-content-wrapper,
    .multi-content-media
      padding: 0
      height: 100%
      background-color: transparent

    .multi-content-media
      max-height: 80px

    &.complete
      background-color: rgba(#222, 0.9)
      color: #fff

    &.active
      background-color: #fff
      color: #222

  .slide-footer
    z-index: 100

  .multi-content-video,
  .multi-content-audio
    pointer-events: all

  &.game-count-down
    .connect-box
      color: transparent
      text-shadow: none

    .multi-content-wrapper
      opacity: 0

  &.state-complete
    .connect-box
      animation: pulse 900ms 600ms 1 forwards

      &.incorrect
        background-color: rgba(#222, 0.4)
        animation-name: rubber
        animation-duration: 600ms
        animation-delay: 0ms
