@use '../../sass/core' as *
@use '../../sass/components/blocks' as *
@use '../../sass/components/content' as *

.slide-carousel
  .carousel
    direction: ltr

  .carousel-item
    float: left
    box-sizing: border-box
    position: relative

    &:before
      @extend %content
      @extend .block-h-md
      @extend .block-dk
      content: ""
      position: absolute
      height: 4px
      left: 0
      border-radius: 0

    &:first-child:before
      content: none

    .direction-rtl &
      float: right

  .carousel-btn
    transition-timing-function: $bounce-lg !important

  &.game-count-down
    .carousel-btn
      opacity: 0 !important

  &.state-complete
    .carousel-item
      &.active .carousel-btn
        animation: rubber 400ms 300ms 1 forwards ease-out

      &.active.carousel-item-correct .carousel-btn
        animation-name: pulse
        animation-duration: 900ms

  .pagination
    position: absolute
    bottom: 1em
    width: 100%
    text-align: center
    direction: ltr
    .page
     display: inline-block
     width: 10px
     height: 10px
     border: 1px solid
     border-radius: 50%
     margin: 0 4px
     cursor: pointer

     &.active
       border-width: 6px
       width: 0
       height: 0
