#slides-view.theme-flat
  background-color: #fafafa !important
  color: #111 !important
  font-weight: 300

  h3, h4, .text-bold,
  strong, b,
  .game-slide .game-stepper,
  .slide-footer .slide-footer-prompt
    font-weight: 400

  h1, h2, .btn
    font-weight: 300

  h4, .text-uppercase,
  .slide-footer .slide-footer-prompt
    text-transform: none !important

  &:after,
  #lesson-title,
  .slide-dial .dial-ring
    border-color: #ddd

  .btn-reveal-desc
    color: #111

  #lesson-pages,
  .connect-box.complete
    background-color: #f4f4f4
    color: inherit

  .btn.btn-solid,
  .btn.btn-muted
    color: #08c
    background: #fafafa

    &.__active,
    &.active
      background: #f3f3f3

  .droppy.active,
  .slider-bar,
  .slider-notch:before,
  .slider-notch:after,
  .connect-box.active,
  .chat-msg.pull-right
    background-color: #ddd

  .connect-box.complete,
  .chat-msg.active,
  .slide-game-jumble .draggy.is-positioned .letter,
  .slide-game-jeopardy .question-answered .block-dk
    color: #fff
    background-color: #08c

  .chat-msg
    &.pull-right:after
      border-top-color: #ddd
      border-right-color: #ddd

    &.active:after
      border-top-color: #08c
      border-right-color: #08c

  .btn.btn-solid
    border: 1px solid #ccc !important

  .selectable,
  .draggy
    .btn.btn-solid
      color: inherit

    &.active .btn.btn-solid
      color: #08c

      &:before
        color: #08c

  .slide-game-jeopardy .game-box,
  .slide-game-elevator .game-box
    border: none

  .slide-game-jumble .droppy,
  .slide-game-elevator .game-box.hide-attributes .block-dk
    background-color: transparent

  .block-dk
    background: rgba(#000, 0.05)

  .number-picker
    background-color: rgba(#000, 0.05)

  #slide-answer
    border-top-color: rgba(#000, 0.05)
    border-top-width: 12px

    .btn.btn-solid
      background-color: transparent
