@use '../../sass/core' as *

$color-border: rgba(0, 0, 0, 0.25)

.slide-quadrant-survey

  .slide-content
    @media (max-width: $screen-md-min)
      .slide-page
        flex-direction: column
        position: relative
        &.slide-page--title
          margin-bottom: 6em
        h2
          margin: auto
        .details
          h2
            display: none
        .btn-circular
          width: 2.5em
          display: inline-block
        .btn-up
          display: none
          position: absolute
          top: -3em
          left: 50%
          z-index: 100
          transform: translateX(-50%)
        .quadrant
          .quadrant-zone
            .btn
              &.btn-sm
                padding: 0.4em 0.5em 0.35em
    @media (min-width: $screen-md-min)
      .slide-page
        flex-direction: row
        &.slide-page--title
          display: none
        .details
          margin-right: 10%
          width: 40%
          display: flex
          order: 1
          flex-direction: column
          justify-content: center
        .quadrant
          margin: 60px 20px
          order: 2
          flex: 1
    .slide-page
      height: 100%
      display: flex
    .btn-circular
      display: none
    .quadrant
      margin: 30px 20px
      padding: 0
      display: flex
      align-items: stretch
      flex-wrap: wrap
      background: rgba(black,0.1)
      list-style: none

      flex-grow: 1
      flex-shrink: 1
      .quadrant-zone
        margin: 0
        padding: 5px
        min-height: 35%
        width: 50%
        box-sizing: border-box
        position: relative

        flex-grow: 1
        flex-shrink: 1
        &:nth-child(1)
          border-right: 1px solid $color-border
          border-bottom: 1px solid $color-border
        &:nth-child(2)
          border-bottom: 1px solid $color-border
        &:nth-child(3)
          border-right: 1px solid $color-border
        .zone-title
          position: absolute
          text-align: center
          &.zone-title--top, &.zone-title--bottom, &.zone-title--left, &.zone-title--right
            right: 0
            left: 0
          &.zone-title--top
            top: -25px
          &.zone-title--bottom
            bottom: -25px
          &.zone-title--left, &.zone-title--right
            top: 50%
          &.zone-title--left
            left: -15px
            transform: translate3d(-50%,-50%,0) rotate(-90deg)
          &.zone-title--right
            right: -15px
            transform: translate3d(50%,-50%,0) rotate(-90deg)
