@use '../../sass/core' as *
@use '../../sass/engine/buttons' as *

.slide-dial-distribution
  .dial
    position: relative
    margin: 1em auto
    width: 18em
    height: 18em

    &:after
      content: ""
      position: absolute
      width: 100%
      height: 100%
      padding: 2em
      left: -2em
      top: -2em

  .dial-ring
    width: 100%
    height: 100%
    border-radius: 18em
    border: 4em solid rgba(0, 0, 0, 0.3)
    box-sizing: border-box

  .dial-knob
    @extend .btn-knob
    position: absolute
    bottom: 100%
    left: 50%
    margin: -0.8em
    z-index: 10
    transform-origin: 50% 9.75em

  .dial-knob-value
    position: absolute
    line-height: 1em
    padding: 4px 6px
    left: 50%
    top: 0
    margin-top: -1.5em
    background: rgba(0, 0, 0, 0.5)
    border-radius: 2px
    min-width: 44px
    transform: translate3d(-50%, 0, 0) scale(1)
    transform-origin: 50% 48px

    @media (max-width: $screen-sm-max)
      margin-top: -28px
      transform-origin: 50% 36px

    &:after
      content: ""
      position: absolute
      left: 50%
      border: 4px solid transparent
      border-top-color: rgba(0, 0, 0, 0.5)
      margin: -4px
      bottom: -4px

  .dial-knob-input
    opacity: 0

  .dial-knob-answer
    opacity: 0
    transition-duration: 600ms

  .dial-label,
  .dial-notch
    position: absolute
    left: 50%

  .dial-notch
    height: 4px
    width: 2px
    margin-left: -1px
    background: rgba(0, 0, 0, 0.3)
    top: -0.5em
    transform-origin: 50% 9.5em

  .dial-label
    height: 1em
    width: 40px
    margin-left: -20px
    top: -3em
    transform-origin: 50% 12em
    opacity: 0.6

  .dial-overlap
    .dial-label
      &:first-child
        margin-left: 0
      &:last-child
        margin-left: -40px
    .dial-notch:last-child
      display: none

  &.active
    .dial-knob-input
      opacity: 1

  &.state-complete
    .dial-knob-input
      background-color: #ccc

      .dial-knob-value
        opacity: 0.3

    .dial-knob-answer
      opacity: 1

  @media (max-width: $screen-sm-max)
    .dial
      font-size: 10px

    .dial-label-text,
    .dial-knob-value
      font-size: 16px
