.slide-find-a-word
  .grid-container
    max-width: 22em
    margin: auto

  .grid
    position: relative
    z-index: 1
    margin: auto

  .grid-row
    height: 0

  .grid-col
    float: left
    height: 100%
    font-size: 1.6em

    &.complete
      color: #000
      transition: color 800ms

  .grid-highlights,
  .grid-highlight
    position: absolute
    top: 0
    left: 0

  .grid-highlights
    width: 100%
    height: 100%
    transform: translate3d(0, 0, 0)
    z-index: -1

  .grid-highlight
    border-radius: 2em
    border: 2px solid
    box-sizing: border-box
    opacity: 0
    background-clip: padding-box

    &.complete
      opacity: 0.6
      background-color: #fff
      border: 1px solid transparent
      transition: all 300ms

    &.incorrect
      transition: all 800ms
      border: 1px solid transparent
      background-color: rgba(#000, 0.2)

    &.active,
    &.incorrect
      opacity: 1

  &.dragging-true
    .grid-highlight
      transition: all 300ms
