@use './icons' as *

%slide-star
  display: inline-block
  width: 1em
  height: 1em
  font-size: 2em
  color: rgba(#000, 0.2)
  margin-left: -0.1em
  margin-right: -0.1em
  @extend %icon
  @extend .icon-star

  &.active
    color: #fecd05
    text-shadow: 0 1px 0 rgba(#000, 0.2)

  &.bunched
    position: relative
    margin-left: -0.18em
    margin-right: -0.18em

    &:nth-child(even)
      top: 0.5em
