@use '../core' as *
#narration-loader
  position: absolute
  top: 0
  left: 0
  bottom: 0
  padding-right: 30px
  padding-left: 6px
  line-height: 2.5em
  z-index: 100
  opacity: 0
  transition: opacity 800ms
  border-radius: 4px 0 0 4px
  background:
    repeat: no-repeat
    position: 75% 50%
    size: 18px
    clip: padding-box

  +screen-lg
    padding-right: 36px
    background-size: 24px

  &:before
    content: "\f11e"
    font-family: icons
    font-weight: normal
    font-style: normal

  .is-loading-narration &
    opacity: 1
    background-image: url(assets/img/templates/loader.gif)
    transition-delay: 0ms
