.slide-video-contribution
  .upload-progress,
  .upload-progress-bar
    width: 100%
    height: 8px
    border-radius: 2px
    overflow: hidden

  .upload-progress-bar
    background: #fff
    transform: translate3d(0, 0, 0) scale(0, 1)
    transform-origin: left center

  .upload-progress
    border: 1px solid transparent

  .upload-progress,
  .upload-video,
  .upload-success,
  .upload-error
    opacity: 0
    transition: opacity 300ms

  .upload-progress,
  .upload-success,
  .upload-error
    position: absolute
    width: 100%
    pointer-events: none

  .upload-progress
    top: 1em

  &.upload-prompt
    .upload-video
      opacity: 1

  &.upload-in-progress
    .upload-progress
      opacity: 1

    .upload-progress-bar
      animation: flash 800ms infinite alternate ease-in-out

  &.upload-complete
    .upload-video,
    .upload-success
      opacity: 1

  &.upload-error
    .upload-video,
    .upload-error
      opacity: 1
