@use '../../sass/core' as *

.slide-number-picker
  .content-fill.invisible
    visibility: hidden

.slide-number-picker
  .number-pickers,
  .number-picker,
  .number-picker-prefix,
  .number-picker-suffix
    display: inline-block

  .number-pickers
    position: relative
    direction: ltr
    height: 80px

  .number-picker
    width: 5em

    + .number-picker
      margin-left: 0.5em

      @media (max-width: $screen-sm-max)
        margin-left: 0.25em

    &.disabled-up .number-picker-arr-up,
    &.disabled-down .number-picker-arr-down
      opacity: 0.5
      pointer-events: none

  @media (max-width: $screen-sm-max)
    .pickers-sm
      .number-picker
        width: 3.2em

      .text-xxl
        font-size: 1.6em

  .number-picker-nums
    height: 80px
    overflow: hidden

  .number-picker-num
    line-height: 80px

  .number-picker-prefix,
  .number-picker-suffix
    position: absolute
    top: 80%

  .number-picker-prefix
    right: 100%
    margin-right: 0.5em

  .number-picker-suffix
    left: 100%
    margin-left: 0.5em

  .number-picker-arr-up,
  .number-picker-arr-down
    position: relative
    z-index: 1

    &:after
      content: ""
      position: absolute
      left: -1em
      right: -1em
      height: 80px

  .number-picker-arr-up
    border-bottom-right-radius: 0
    border-bottom-left-radius: 0

    &:after
      top: -1em

  .number-picker-arr-down
    border-top-right-radius: 0
    border-top-left-radius: 0

    &:after
      bottom: -1em

  .text-fixed
    font-size: 26px !important
    height: 80px
