.slide-peer-authoring

  h1
    font-weight: normal

  li
    list-style-type: none

  h1.js-progress
    font-size: 2.7em

  .upload-view
    margin: 0
    height: inherit
    display: flex
    flex-direction: column
    align-items: center
    justify-content: space-between

    .upload-form
      width: 300px
      display: flex
      flex-direction: column
      align-items: center

      label
        margin-top: 1em
        width: 80%
        text-align: left
        text-transform: uppercase

      input
        padding: 10px 0
        width: 80%
        border: solid 2px white
        border-top: none
        border-right: none
        border-left: none
        border-radius: 0
        background-color: transparent
        color: white

        &:focus
          outline: none

        &::placeholder
          color: #CCCCCC

    .submit-button
      margin-top: 3em
      padding: 0.5em
      width: 100%
      border: solid 2px white
      border-radius: 4px
      font-weight: normal
      font-size: 1.5em
      
  .multi-content-video:not(.vjs-fullscreen)
    video
      max-height: unset!important

  .js-item-list
    padding-right: 0
    padding-left: 0

  .multi-content-wrapper
    margin: 10px auto 0
    transition: margin 500ms

    .block-lt
      padding: 1em 1.3em 0.8em
      border-radius: 6px
      background: rgba(255, 255, 255, 0.2)

    .video-js .vjs-big-play-button
      background-color: rgba(51, 51, 51, 0.8)
      -webkit-transform: translateZ(0)

      -webkit-backface-visibility: hidden  // http://help.dimsemenov.com/discussions/problems/858-weird-bug-in-chrome

    &.selected
      padding-bottom: 100vh

  .video-info
    margin: 10px 1em 0
    padding: 10px 20px 12px
    display: flex
    flex-direction: column
    justify-content: space-around
    background-color: white
    color: black
    font-size: 1.25em

    .video-actions
      margin-bottom: 5px
      width: 100%
      display: flex
      justify-content: space-between

      .icon-group-entry
        display: flex

      .icon
        font-size: 1.1em

      .icon-group-entry
        margin-right: 8px

      .icon-chat-count:before
        padding-right: 4px
        color: rgb(92, 176, 212)

      .icon-chat:before
        padding-right: 4px
        color: rgb(92, 176, 212)

      .icon-heart:before
        color: rgb(227, 53, 41)

      .video-like-count, .video-comment-count
        padding-top: 4px
        font-size: 18px

    .video-title
      margin-top: 5px

  .btn.btn-solid.btn-upload
    padding: 0.9em 0.75em 0.8em
    width: 100%
    position: absolute
    bottom: 2%
    font-weight: normal
    font-size: 1.2em
    opacity: 1
    transition: transform 500ms ease-in-out, opacity 500ms ease-in-out

    &--hidden
      opacity: 0
      transform: translate3d(0, 150px, 0)

    &--disabled
      border-color: #DDDDDD
      background-color: #DDDDDD
      color: #AAAAAA
      cursor: default

  .upload-progress-indicator
    display: flex
    align-items: center
    justify-content: center
    border-radius: 100%
    color: rgb(70, 180, 233)
    background-color: white
    animation: fade-in 0.5s
    height: 10em
    width: 10em

    &--hidden
      display: none

    .upload-progress-text
      font-size: 3em
      margin: 35px 15px

  @keyframes fade-in
    from
      opacity: 0
    to
      opacity: 1

  .loading-indicator
    width: 64px
    height: 64px
    display: inline-block
    position: relative

  .loading-indicator div
    margin: 6px
    width: 51px
    height: 51px
    display: block
    box-sizing: border-box
    position: absolute
    border: 6px solid #FFFFFF
    border-color: #FFFFFF transparent transparent transparent
    border-radius: 50%
    animation: loading-indicator 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite

  .loading-indicator div:nth-child(1)
    animation-delay: -0.45s

  .loading-indicator div:nth-child(2)
    animation-delay: -0.3s

  .loading-indicator div:nth-child(3)
    animation-delay: -0.15s

  @keyframes loading-indicator
    0%
      transform: rotate(0deg)

    100%
      transform: rotate(360deg)
