.slide-ratio
  .ratio-bar-container
    height: 20px
    border: 3px solid rgba(#000, 0.2)
    overflow: hidden
    position: relative
    z-index: 1
    background-color: rgba(#000, 0.4)
    background-clip: padding-box
    clear: both

    .direction-ltr &
      border-bottom-right-radius: 0px

    .direction-rtl &
      border-bottom-left-radius: 0px

  .ratio-bar
    height: 20px
    background-color: #fafafa
    border-radius: 2px
    width: 100%
    transform-origin: left center

    .direction-rtl &
      transform-origin: right center

  .ratio-value
    display: block
    position: relative
    z-index: 0

  .ratio-value-amount
    border-radius: 0 0 4px 4px
    padding: 2px 4px
    background-color: rgba(#000, 0.2)

  &.game-count-down
    .ratio-value-amount
      color: transparent
      text-shadow: 0 0 transparent

    .ratio-bar
      opacity: 0

    .ratio
      pointer-events: none

  &.state-complete
    .incorrect .ratio
      opacity: 0.8
    .incorrect .ratio-title
      animation: rubber 700ms 200ms 1 forwards ease-out
    .correct
      animation: pulse 1200ms 700ms 1 forwards ease-out
