@use '../../core/mixin' as *

.pswp
  display: none
  position: absolute
  color: #fff
  top: 0
  left: 0
  right: 0
  bottom: 0
  overflow: hidden
  -ms-touch-action: none
  touch-action: none
  z-index: 1500
  @include backface-visibility(hidden)
  outline: none

  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)
    position: absolute !important

  img
    max-width: none

.pswp--animate_opacity
  opacity: 0.001
  will-change: opacity
  transition: opacity 333ms cubic-bezier(0.4, 0, 0.22, 1)

.pswp--open
  display: block

.pswp--zoom-allowed .pswp__img
  cursor: -webkit-zoom-in
  cursor: -moz-zoom-in
  cursor: zoom-in

.pswp--zoomed-in .pswp__img
  cursor: -webkit-grab
  cursor: -moz-grab
  cursor: grab

.pswp--dragging .pswp__img
  cursor: -webkit-grabbing
  cursor: -moz-grabbing
  cursor: grabbing

.pswp__bg
  position: absolute
  left: 0
  top: 0
  width: 100%
  height: 100%
  background: #000
  opacity: 0
  @include backface-visibility(hidden)
  will-change: opacity

.pswp__scroll-wrap
  position: absolute
  left: 0
  top: 0
  width: 100%
  height: 100%
  overflow: hidden

.pswp__container, .pswp__zoom-wrap
  -ms-touch-action: none
  touch-action: none
  position: absolute
  left: 0
  right: 0
  top: 0
  bottom: 0

.pswp__container, .pswp__img
  user-select: none

.pswp__zoom-wrap
  position: absolute
  width: 100%
  transform-origin: left top
  transition: transform 333ms cubic-bezier(0.4, 0, 0.22, 1)

.pswp__bg
  will-change: opacity
  transition: opacity 333ms cubic-bezier(0.4, 0, 0.22, 1)

.pswp--animated-in
  .pswp__hint
    opacity: 0
  .pswp__counter
    opacity: 1
  .pswp__bg, .pswp__zoom-wrap
    transition: none

.pswp__container, .pswp__zoom-wrap
  @include backface-visibility(hidden)

.pswp__item
  position: absolute
  left: 0
  right: 0
  top: 0
  bottom: 0
  overflow: hidden

.pswp__img
  position: absolute
  width: auto
  height: auto
  top: 0
  left: 0

  .pswp--smooth-exit &
    transition: 300ms
    opacity: 0

.pswp__img--placeholder
  @include backface-visibility(hidden)

.pswp__img--placeholder--blank
  background: transparent

.pswp--ie .pswp__img
  width: 100% !important
  height: auto !important
  left: 0
  top: 0

.pswp__error-msg
  position: absolute
  left: 0
  top: 50%
  width: 100%
  text-align: center
  font-size: 14px
  line-height: 16px
  margin-top: -8px
  color: #CCC

  a
    color: #CCC
    text-decoration: underline
