@use '../../sass/engine/buttons' as *

.slide-slider
  .slider
    position: relative
    padding: 3em 0 4em
    width: 600px
    max-width: 90%
    margin: 4em auto 0
    direction: ltr

  .slider-bar
    height: 4px
    background: rgba(0, 0, 0, 0.3)
    border-radius: 2px
    margin: 0 -8px

  .slider-knob
    @extend .btn-knob
    position: absolute
    margin: -0.8em
    z-index: 10

  .slider-knob-value
    position: absolute
    line-height: 1em
    padding: 5px 6px 4px
    top: -16px
    left: 50%
    margin-top: -1em
    background: rgba(0, 0, 0, 0.3)
    border-radius: 2px
    min-width: 44px
    transform: translate3d(-50%, 0, 0) scale(1)
    transition: all 300ms

    &:after
      content: ""
      position: absolute
      left: 50%
      border: 4px solid transparent
      border-top-color: rgba(0, 0, 0, 0.3)
      margin: -4px
      bottom: -4px

  .slider-knob-input
    opacity: 0

  .slider-knob-answer
    opacity: 0

  .slider-label
    position: absolute
    width: 40px
    margin: 0 -20px
    padding-top: 12px
    opacity: 0.8

  .slider-notch
    position: absolute

    &:before,
    &:after
      content: ""
      position: absolute
      left: 50%
      height: 4px
      width: 2px
      margin-left: -1px
      background: rgba(0, 0, 0, 0.3)

    &:before
      top: -8px
      border-top-left-radius: 1px
      border-top-right-radius: 1px

    &:after
      top: 0
      border-bottom-left-radius: 1px
      border-bottom-right-radius: 1px

  &.active
    .slider-knob-input
      opacity: 1

  &.state-complete
    .slider-knob-input
      background-color: #ccc

      .slider-knob-value
        opacity: 0.3

    .slider-knob-answer
      opacity: 1
