.slide-strikeout
  .strikeout-canvas
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%

  .strikeout-canvas-container
    z-index: 0

  .strikeout-words
    pointer-events: none

  .word
    display: inline-block
    border-radius: 4px
    transition: all 300ms
    max-width: calc(100% - 0.2em)

    &.active
      background-color: #fff
      color: #222
      animation: rubber 300ms 1 forwards

      .text-dark &
        background-color: rgba(#000 ,0.8)
        color: #fff

  .word-incorrect
    &.active
      background-color: #222
      color: #fff
      animation: none
