@use 'sass:color'
.slide-categorise
  .draggy-parent
    position: relative

  .droppy
    pointer-events: none

  .droppy-child
    position: relative
    transition: all 300ms !important
    transition-delay: inherit !important

  .btn-text
    padding: 0 6px

  .draggy
    z-index: 2
    position: relative

    &.draggy-drop
      padding-left: 6px
      padding-right: 6px

      .btn-text
        padding: 0

  .droppy
    &.active
      .droppy-child
        background-color: color.adjust(#000, $alpha: -0.6)
        opacity: 1

  &.game-count-down
    .droppy-child
      color: transparent
      text-shadow: none

  &.state-complete
    .droppy-false.active
      .droppy-child
        background-color: color.adjust(#000, $alpha: -0.8)
        animation: rubber 600ms 1

    .droppy-true,
    .droppy-true.active
      .droppy-child
        opacity: 1
        background-color: color.adjust(#000, $alpha: -0.6)
        animation: pulse 600ms 1
