#play-audio-popup
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  opacity: 0
  z-index: -1
  transition: opacity 300ms, z-index 0ms 300ms
  color: #fff
  background: rgba(#000, 0.8)

  .btn-no
    position: absolute
    bottom: 1em
    left: 2em
    right: 2em
    opacity: 0.4

  &.active
    opacity: 1
    z-index: 100
    transition-delay: 0ms, 0ms
    -webkit-backdrop-filter: blur(4px)
