.slide-youtube-video-embed
  .yt-player,
  .yt-thumb
    border: none
    width: 100%
    display: block
    transition: opacity 300ms

  .yt-content
    overflow: hidden
    .device-android &
      bottom: 0

  .yt-player
    opacity: 0
    height: 100%
    position: absolute

  .yt-thumb
    z-index: 1
    position: absolute
    pointer-events: none

  &.video-playing
    .yt-thumb
      opacity: 0

    .yt-player
      opacity: 1
