@use '../core' as *
@use './float' as *
// Center content horizontally and vertically
=make-content($type)
  .content#{$type},
  .content#{$type}-ver,
  .content#{$type}-hor,
  %content#{$type},
  %content#{$type}-ver,
  %content#{$type}-hor
    position: relative

  .content#{$type},
  %content#{$type}
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)

  .content#{$type}-ver,
  %content#{$type}-ver
    top: 50%
    transform: translate(0, -50%)

  .content#{$type}-hor,
  %content#{$type}-hor
    left: 50%
    transform: translate(-50%, 0)

  .content#{$type}-fill
    box-sizing: border-box
    height: 100%

  .content#{$type}-scroll,
  %content#{$type}-scroll
    overflow: auto
    height: 100%
    box-sizing: border-box

    &.content#{$type},
    &.content#{$type}-ver
      height: auto
      max-height: 100%
      max-width: 100%

    .state-complete &
      overflow: hidden
      opacity: 0.99

.hide-overflow
  overflow: hidden

.content-fit
  &.fitting
    &.content-fit-clone
      .content-fill,
      .content-lg-fill,
      .content-md-fill,
      .content-sm-fill
        height: auto
        max-height: 100%

    *
      transition: none !important

#content-spacer
  height: 1px
  position: relative
  clear: both
  @extend .clearfix

+make-content("")

@media (max-width: $screen-md-max)
  +make-content("-sm")

@media (min-width: $screen-md-min)
  +make-content("-md")

@media (min-width: $screen-lg-min)
  +make-content("-lg")

.content-scroll-inner
  margin: auto
  overflow: hidden

  +screen-landscape
    max-width: 80%

  +screen-lg
    max-width: 80%
