@use "." as *

html,
body
  width: 100%
  height: 100%
  &.disable-select
    -webkit-touch-callout: none
    -webkit-user-select: none
    -khtml-user-select: none
    -moz-user-select: none
    -ms-user-select: none
    user-select: none

body
  cursor: default
  margin: 0
  padding: 0
  position: relative
  overflow: hidden
  color: #333
  font-family: $font-name, sans-serif
  font-weight: normal
  font-size: 17px
  line-height: 1.2
  text-rendering: optimizeLegibility
  background-size: cover
  background-color: #000
  user-select: none
  -webkit-user-select: none
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0)
  -webkit-touch-callout: none
  -webkit-text-size-adjust: none
  -webkit-overflow-scrolling: touch
  -ms-overflow-style: -ms-autohiding-scrollbar

  &.slides-active
    touch-action: pinch-zoom

  @media (max-width: $screen-md-max)
    font-size: 16px

  +screen-sm
    font-size: 15px

  @media (max-width: $screen-xs-max)
    font-size: 14px

  @-ms-viewport
    width: device-width
    height: device-height
a
  color: inherit

button, input, textarea
  font: inherit

// Temporarily addresses focus being seen when user interacts
// with slide templates while screen reader is off
*:focus
  outline: transparent

audio:focus, video:focus
  outline: none

@media (max-width: $screen-sm-max), (max-height: $screen-sm-max)
  .hidden-sm
    display: none

@media (min-width: $screen-sm-min)
  .hidden-lg
    display: none
