.slide-missing-word
  .word
    display: inline-block
    height: 1.8em
    line-height: 2.3em
    margin: 0 0.1em
    max-width: 100%

  .word-text
    visibility: hidden
    padding: 0 0.1em

  .draggy
    margin: 0.2em
    padding: 0.7em 0.5em

  .droppy
    border-bottom: 2px solid
    transition: background-color 300ms
    position: absolute
    top: 0
    left: 0
    right: 0
    bottom: 0em
    border-radius: 4px 4px 0 0

    &.active
      background-color: rgba(black, 0.25)

