@use '../core' as *
@use './stars' as *
#stars-earned
  position: absolute
  opacity: 0
  transition: all 600ms
  pointer-events: none
  color: #fff
  background: rgba(#000, 0.85)
  border-radius: 1em
  padding: 1em 1.2em 1.2em
  transform: translate3d(-50%, -100%, 0)
  z-index: 100

  .star
    @extend %slide-star
    color: rgba(#aaa, 0.6)
    line-height: 1
    opacity: 0
    transform: translate3d(0, 0, 0) scale(0.5)
    transition: all 800ms $bounce-md
    margin-bottom: 12px

    &.bunched
      margin-top: -0.1em
      margin-bottom: 0.9em

  @for $i from 1 through 10
    .delay-#{$i}
      transition-delay: $i * 80ms

  &.before-display
    transform: translate3d(-50%, 0%, 0)

  &.display
    opacity: 1
    transform: translate3d(-50%, -50%, 0)

    .star
      opacity: 1
      transform: translate3d(0, 0, 0) scale(1)
