@use '../../sass/core' as *

.slide-parallax
  .parallax-container
    .ui-minimal &
      padding-left: 0
      padding-right: 0

  .parallax-images
    cursor: move

  .parallax-img
    max-width: 100%
    max-height: 100%
    position: absolute

  .caption
    position: absolute
    bottom: 0
    left: 0
    right: 0

    .ui-minimal &
      padding-bottom: 6em

      +screen-lg
        padding-bottom: 10em
