@use '../core' as *
// Wrapping
.text-nowrap
  white-space: nowrap
.text-pre
  white-space: pre-line
.text-prewrap
  white-space: pre-wrap
.text-ellipsis
  text-overflow: ellipsis
  white-space: nowrap
  overflow: hidden

// Proper wrapping for Chinese, Korean, Japanese as well
.text-break
  overflow-wrap: break-word
  word-break: keep-all

:lang(zh), :lang(ja) // break normally for these languages since they don't have spaces
  .text-break
    word-break: normal

// Weight
h1, h2, h3, .text-normal, .text-light
  font-weight: 400
h4, .text-bold
  font-weight: 600

// Transformation
.text-lowercase
  text-transform: lowercase
h4, .text-uppercase
  text-transform: uppercase
.text-capitalize
  text-transform: capitalize
.text-untransform
  text-transform: none

// Margin and spacing
h1, h2, h3, h4, ul, ol, li, p, hr
  margin: 0 0 0.5em

  &:last-child
    margin-bottom: 0

// Lists
ul, ol
  text-align: left
  text-align: start
  padding: 0 1.7em

  &.md-list
    padding: 0
    list-style-type: none

    > li
      position: relative

      &:before
        position: absolute

  .direction-rtl &
    text-align: right
    text-align: start

  .text-center &
    display: table
    margin-left: auto
    margin-right: auto

ul.md-list
  list-style: disc

  > li
    padding: 0 0.5em
    margin-inline-start: 1em

ol.md-list
  counter-reset: ol

  > li
    padding: 0 1.6em

    &:before
      counter-increment: ol
      content: counter(ol) "."
      width: 1.6em
      margin: 0 -1.6em

.tight
  margin-bottom: 0

.text-muted
  color: #b4bcc6

// Borders
hr
  border: none
  border-top: 1px solid

// Leading
.lead-tight
  line-height: 1
.lead-normal
  line-height: 1.2
.lead-loose
  line-height: 1.5

// Font Sizes
h1, .text-xxl
  font-size: 1.75em
.text-xl
  font-size: 1.5em
h2, .text-lg
  font-size: 1.25em
h3, .text-md
  font-size: 1.1em
h4, .text-sm
  font-size: 0.8em

+screen-lg
  h1, .text-xxl
    font-size: 2em
  .text-xl
    font-size: 1.66em
  h2, .text-lg
    font-size: 1.33em
  h3, .text-md
    font-size: 1.16em
