.multi-content-wrapper
  position: relative
  max-width: 480px
  margin: auto

.multi-content
  max-width: 100%
  display: block
  margin: 0 auto


audio.multi-content,
video.multi-content
  width: 100%

// for hiding audio elements until they are properly sized
audio.multi-content
  visibility: hidden

video.multi-content
  height: 150px
  background: transparent

img.multi-content
  max-height: 160px

.multi-content-media
  overflow: hidden

  .video-js
    font-size: 12px
    background: transparent
    padding-top: 0 !important
    height: auto !important

    video
      position: relative
      max-height: 140px
      z-index: -1

    .vjs-big-play-button
      font-size: 0.75rem

    .vjs-control-bar
      width: auto
      left: -4px
      right: -4px
      bottom: -4px

    &.vjs-fullscreen
      video
        position: absolute
        top: 0
        bottom: 0
        left: 0
        width: 100%
        height: 100%
        border: 0
        max-height: none

.multi-content-iframe
  iframe
    display: block
    border: none
    width: 100% !important
    height: auto !important
