@use '../../sass/core' as *
@use '../../sass/core/mixin' as *

.slide-game-elevator
  .game-box
    display: flex
    flex-direction: column
    transition: opacity 300ms

    &.hide-attributes
      position: relative
      overflow: auto

      .block-dk
        transition: background-color 300ms
        background-color: transparent

    &.hide-description
      opacity: 0

    +screen-lg
      box-sizing: border-box

  .btn-attribute
    &:first-child
      border-top-left-radius: 4px
      border-top-right-radius: 4px

    &:last-child
      border-bottom-left-radius: 4px
      border-bottom-right-radius: 4px

    &.selected
      transition: all 300ms
      pointer-events: none
      background-color: #fff !important
      color: #333

    @media (max-width: $screen-sm-max)
      font-size: 0.9em

  .game-description
    display: flex
    flex-direction: column
    height: 100%

    div:first-child
      margin-top: auto
    div:last-child
      margin-bottom: auto

  .game-image
    max-width: 100%
    max-height: 150px

    @media (max-width: $screen-sm-max)
      max-height: 80px

  .game-prompt
    position: absolute

  .game-prompt
    bottom: 0.75em
    left: 0
    right: 0

  .game-image-title
    padding: 1.28em 0.746em 0.426em
    @include multi-line-ellipsis(2)
