@use '../core' as *
#lesson-header
  position: absolute
  left: 0
  right: 0
  line-height: 1
  pointer-events: none
  transition: opacity 300ms
  z-index: 10

  .icon:before
    vertical-align: middle

  .ui-minimal &,
  .display-stars-avail &
    opacity: 0

#lesson-header-title
  max-width: 100%

#lesson-header-title-content,
#lesson-header-nav-page-count,
#lesson-header-nav-social-learning,
#lesson-header-nav-menu-btn
  display: inline-block
  padding: 0.6em 0.8em
  height: $lesson-header-height
  box-sizing: border-box
  vertical-align: middle

#lesson-header-title-content
  padding: 0.5em 0 0.9em
  border-bottom: 1px solid
  transition: all 300ms
  display: block

#lesson-header-title-logo
  vertical-align: middle
  height: $lesson-header-height
  transition: transform 300ms
  transform-origin: top left
  transform: scale3d(1, 1, 1)

  .direction-ltr &
    float: left
    margin-right: 1em

  .direction-rtl &
    float: right
    margin-left: 1em

#lesson-header-nav
  border-radius: 4px
  color: #fff
  background: rgba(#000, 0.25)
  float: right
  transition: all 200ms
  position: relative

  .direction-ltr &
    float: right
    margin-left: 1.2em

  .direction-rtl &
    float: left
    margin-right: 1.2em

#lesson-header-nav-page-count
  line-height: 1.25

  small
    margin-left: 0.5px

  .is-loading-narration &
    opacity: 0

#lesson-header-nav-social-learning
  display: none
  .icon
    position: relative
    display: inline-block
    font-size: 24px
    line-height: 24px
    vertical-align: middle
    height: 1em
    margin: -4px -3px

    .device-ios &
      margin-top: -5px

  .icon-count
    position: absolute
    right: 1px
    right: 1.2px
    top: 1px
    height: 1em
    width: 1em
    font-size: 11px
    font-weight: bold
    line-height: 1.1em
    vertical-align: middle
    display: none

    .device-ios &
      top: 2px

  &.has-count
    .icon-chat:before
      content: "\F134"

    .icon-count
      display: block

  .ui-is-social &
    display: inline-block

#lesson-header-nav-menu-btn,
#lesson-header-nav-social-learning
  border-left: 1px solid rgba(#000, 0.1)
  padding: 0 0.65em
  line-height: $lesson-header-height
  pointer-events: all
  transition: inherit
  text-align: center
  cursor: pointer
  margin-left: -1px

  .icon
    display: inline-block

  &.__active
    background-color: rgba(#000, 0.25)
    transition: background 300ms

  .ui-minimal &
    pointer-events: none

  .direction-rtl &
    border-left: none
    border-right: 1px solid rgba(#000, 0.1)

#lesson-header-nav-menu-btn
  border: none
  background-color: rgba(#000, 0.01)
  color: inherit
  cursor: pointer
  border-radius: 0 4px 4px 0

  .icon
    .device-ios &
      margin-top: -2px

.scroll-middle,
.scroll-bottom
  #lesson-header-title-logo
    transform: scale3d(0.75, 0.75, 0.75)

    + #lesson-header-title-content
      transform: translate3d(-0.75em, 0, 0)

  #lesson-header-nav
    background: transparent
    transform: translate3d(0.75em, -0.2em, 0)

  #lesson-header-nav-menu-btn,
  #lesson-header-title
    border-color: transparent
