@use '../../sass/core' as *

.slide-image-slider
  .carousel-slider
    position: relative
    z-index: 1

  .caption
    position: absolute
    width: 100%
    opacity: 0
    transition-property: none

    &.position-top
      top: 2.5em

    &.position-middle
      top: 50%
      transform: translate3d(0, -50%, 0) scale(0.9)

    &.position-bottom
      bottom: 2.5em
      transform: translate3d(0, 50%, 0)

  .style-default
    .caption-content
      background: #fff
      color: #000
      box-shadow: 0 2px 4px rgba(#000, 0.2)

  .style-transparent-light-text,
  .style-transparent-dark-text
    .caption-content
      background: transparent
      box-shadow: none

  .style-transparent-dark-text
    .caption-content
      color: black

  .style-transparent-light-text
    .caption-content
      color: white

  .style-dark
    .caption-content
      background: rgba(0, 0, 0, 0.25)
      color: white

  .style-light
    .caption-content
      background: rgba(255, 255, 255, 0.25)
      color: black

  .img-pos-top,
  .img-pos-bottom
    .img
      position: absolute
      transform: translate(-50%, 0)
      .device-ios &[src$=".svg"]
        object-fit: none

  .img-pos-top
    .img
      top: 0

  .img-pos-bottom
    .img
      top: auto
      bottom: 0

  .direction-rtl &
    .img-pos-middle
      .img
        left: unset
        right: 50%
        transform: translate(50%, -50%)

  .img-pos-cover
    position: absolute
    background-repeat: no-repeat
    top: 0
    left: 0
    right: 0
    bottom: 0
    display: block
    z-index: 1
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    max-width: none
    max-height: none
    .full-screen
      top: -3.9em
      +device-iphonex
        top: -6.18em
        bottom: -5.72em
      +screen-lg
        top: -4.9em
      .device-ios &
        margin-top: calc(-1 * (constant(safe-area-inset-top) + 4px))
        margin-top: calc(-1 * (env(safe-area-inset-top) + 4px))
        margin-bottom: calc(-1 * constant(safe-area-inset-bottom))
        margin-bottom: calc(-1 * env(safe-area-inset-bottom))

  .btn-up,
  .btn-down
    position: absolute
    left: 50%
    margin: 0 -1.25em
    display: none
    transition: all 300ms
    z-index: 100
    border: none
    color: inherit

  &.caption-position-none,
  &.caption-position-middle,
  &.caption-position-bottom,
  &.caption-position-top
    .btn-up.active,
    .btn-down.active
      display: inline-block

  .btn-up
    top: 1em

    .ui-minimal &
      transform: translate3d(0, -3em, 0)

  .btn-down
    bottom: 0

  .carousel-item
    position: relative
    pointer-events: none

    &.transition
      .caption
        transition: all 300ms

    &.active
      pointer-events: auto

      .caption
        opacity: 1
        transform: translate3d(0, 0, 0)
        transition-delay: 300ms
        transition-duration: 600ms

        &.position-middle
          transform: translate3d(0, -50%, 0) !important

  .img
    max-width: 100%
    max-height: 100%

  .carousel-content
    opacity: 0

  ul, ol
    display: block
