.slide-image
  .img
    max-width: 100%
    max-height: 100%
    transition: opacity 300ms 150ms
    opacity: 0

  .img-compose
    border-radius: 4px
    box-shadow: 0 2px 8px rgba(#000, 0.2)
    box-sizing: border-box
    padding: 12px
    background-color: #fff

  .img-container
    display: inline-block
    height: 100%
    position: relative

  .caption
    position: absolute
    bottom: -8em
    border-bottom: 8em solid transparent
    left: 0
    right: 0
    color: inherit
    pointer-events: none
    z-index: 1
    opacity: 0
    transform: translate3d(0, 2em, 0)
    transition: transform 300ms, opacity 300ms

  &:after,
  .slide-footer
    z-index: 2

  &.state-narration
    pointer-events: all

  &.active.loaded-true
    .img
      opacity: 1

    .slide-loader
      opacity: 0

  &.focus-true
    .img
      opacity: 0

  &.hide-caption
    .slide-footer
      opacity: 0
      pointer-events: none

