@use '../../sass/core/mixin' as *

.slide-reveal
  .break-word
    hyphens: auto
    // IE hyphenation is substandard
    -ms-hyphens: none

  .content-pieces
    margin: 0 -0.5em
    display: flex
    flex-wrap: wrap
    justify-content: center

  .content-piece
    float: none
    padding-left: 0.5em
    padding-right: 0.5em
    min-width: 20%
    flex: 0 0 auto

  .btn-reveal
    position: relative
    padding: 0
    width: 100%
    height: 100%
    perspective: 1000
    display: inline-block

    &.active
      transform-style: preserve-3d

      .btn-reveal-title

        &.flippable
          transform: rotateX(180deg)

        &.ie
          display: none

      .btn-reveal-desc

        &.flippable
          transform: rotateX(0deg)

        &.ie
          display: block

  .btn-reveal-title,
  .btn-reveal-desc
    @include backface-visibility(hidden)
    transition: transform 500ms
    box-sizing: border-box
    width: 100%
    height: 100%

  .btn-reveal-title

    &.flippable
      position: absolute
      top: 0
      transform: rotateX(0deg)

    &.ie
      display: block

  .btn-reveal-desc

    &.flippable
      transform: rotateX(-180deg)

    &.ie
      display: none
