@use '../../sass/core' as *

.slide-image-map
  .caption
    pointer-events: none
    transition: all 300ms
    position: absolute
    left: 0
    right: 0
    bottom: 0
    margin-bottom: -8em
    padding-bottom: 12em
    opacity: 0.8
    transform: translate3d(0, 2em, 0)
    background: linear-gradient(to top, #111 80%, rgba(#111, 0.6))
    background-repeat: no-repeat

    +screen-lg
      padding-bottom: 14em

  .image-container
    margin: auto
    max-height: 100%
    max-width: 100%
    position: relative

  .img-map,
  .waypoint,
  .waypoint-img
    transition: opacity 400ms linear, transform 400ms
    opacity: 0

  .navigation
    bottom: -1px
    right: 0
    left: 0
    position: absolute
    transition: all 300ms
    pointer-events: none

  .full-screen
    pointer-events: none

  .img-map
    display: block
    margin: 0 auto
    max-height: 100%
    max-width: 100%


  .waypoints.content
    direction: ltr

  .caption
    opacity: 0

  .waypoints,
  .waypoint,
  .waypoint-img
    position: absolute

  .waypoint-img
    top: 50%
    width: 100%
    pointer-events: none

    img
      max-width: 100%
      max-height: 100%

    &.active
      opacity: 1

  .waypoint
    cursor: pointer
    animation: pulse 2s infinite alternate linear
    border-radius: 4px
    position: absolute
    box-sizing: border-box
    top: 0
    left: 0
    right: 0
    bottom: 0
    transition: all 300ms

  &.waypoint-white
    .waypoint
      border: 2px solid rgba(#fff, 0.6)
      box-shadow: 0 0 3px rgba(#000, 0.2)

  &.waypoint-black
    .waypoint
      border: 2px solid rgba(#000, 0.6)
      box-shadow: 0 0 3px rgba(#fff, 0.2)

  &.waypoint-none
    .waypoint
      animation: none

  &.caption-true
    .caption
      opacity: 1
      position: relative
      transform: translate3d(0, 0, 0)

  &.is-loaded
    .img-map,
    .waypoint
      opacity: 1

    .waypoint.tapped
      opacity: 0.5

  &.zoom-true
    .img-map,
    .waypoint,
    .waypoint.tapped
      opacity: 0
      pointer-events: none

  &.sticky-true
    .caption
      padding-top: 0.8em

  &.recenter-true
    .img-map,
    .waypoint-img
      transition-timing-function: ease-in-out

  &.no-transition
    .img-map,
    .waypoint,
    .caption,
    .navigation
      transition: none !important
