@use '../../sass/core' as *
@use '../../sass/core/mixin' as *
@use '../../sass/components/content' as *

.slide-game-memory
  .game-tiles
    margin: 0 -0.5em
    padding: 0.5em 0

  .tile
    float: left
    box-sizing: border-box
    padding: 0.5em

    &.active
      .tile-front
        transform: translate3d(0, 0, 0) rotateX(180deg)
      .tile-back
        transform: translate3d(0, 0, 0) rotateX(0deg)
    &.correct
      opacity: 0.5

  .tile-2
    height: 50%
    width: 50%

  .tile-3
    height: 33.33%
    width: 50%

  .tile-4
    height: 50%
    width: 25%
    @media (max-width: $screen-sm-max)
      height: 25%
      width: 50%

  .tile-content
    height: 100%
    position: relative
    display: block
    .btn
      .text-xl, .multi-content-text
        @include backface-visibility(hidden)

  .tile-front,
  .tile-back
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    @include backface-visibility(hidden)
    transition: transform 500ms
    transform-style: preserve-3d

  .tile-front
    transform: translate3d(0, 0, 0) rotateX(0deg)

  .tile-back
    position: absolute
    transform: translate3d(0, 0, 0) rotateX(180deg)

  .multi-content-wrapper,
  .multi-content-media
    height: 100%
    box-sizing: border-box

  .multi-content-media
    padding: 0

  .multi-content-image
    max-height: 100%

  .multi-content-text
    @extend %content

  &.game-paused
    .slide-footer-prompt
      opacity: 0 !important
      pointer-events: none !important
