.slide-image-pair
  .image-container
    transition: all 300ms
    position: relative

    &:after
      content: ""
      position: absolute
      top: 50%
      width: 3em
      height: 4px
      margin-top: -2px
      background-color: rgba(#000, 0.08)

    &.left:after
      right: -3em

    &.right:after
      left: -3em

    .direction-rtl &
      &.left:after
        left: -3em
        right: auto

      &.right:after
        right: -3em
        left: auto

    &.active
      background: rgba(#000, 0.3)

  .img
    height: auto
    width: auto
    max-height: 100%
    max-width: 100%
    min-width: 50px
    min-height: 30px
    box-sizing: border-box
    margin: auto

  &.state-complete
    .image-container.active
      &.correct
        animation: pulse 900ms 600ms 1 forwards
      &.incorrect
        animation: rubber 600ms 1 forwards

  .show-incorrect
    animation: rubber 600ms 1 forwards
