.slide-vimeo-video-embed
  .vimeo-player,
  .vimeo-thumb
    border: none
    width: 100%
    display: block
    transition: opacity 300ms

  .vimeo-content
    overflow: hidden

  .vimeo-player
    opacity: 0
    height: 100%
    position: absolute

    iframe
      height: 100%
      width: 100%

  .vimeo-thumb
    z-index: -1
    position: absolute
    opacity: 0
    &.show
      opacity: 1

  &.thumb-false
    .vimeo-thumb
      background: #1a1a1a
      height: 75%
      opacity: 1
      border: 0

  &.video-playing
    .vimeo-thumb
      opacity: 0

    .vimeo-player
      opacity: 1
