.slide-multiple-choice-game
  ul
    width: 100%
    padding: 0
    table-layout: fixed

  li
    list-style: none
    .btn-block
      width: 100%

  .inner-content
    overflow: hidden

  .col
    display: flex
    flex-direction: column
    justify-content: center
    min-height: 100%

    // IE11 fix where min-height of flex container isn't respected,
    // causing alignment to fail
    @media (-ms-high-contrast:none)
      height: 1px

  .slide-title,
  .slide-subtitle
    // IE11 fix for subtitle overlapping title when title overflows
    // slide height, and flex-shrink is enabled by default
    flex-shrink: 0

  .answers
    // IE11 fix where dark blue background around answers did not expand
    // to fit all answers when answers overflow beyond slide height
    flex-shrink: 0

  .selectable.fade-in
    opacity: 0

  &.image-focus-true
    .multi-content-wrapper-image:after
      content: "\f108"
      font-family: icons
      font-size: 1.6em
      line-height: 1
      position: absolute
      top: 4px
      right: 4px
      width: 1em
      height: 1em
      color: #fff
      background: rgba(#000, 0.12)
      border-radius: 4px
  
  &.game-playing,
  &.game-outro
    .selectable.fade-in
      opacity: 1

  &.state-complete
    .selectable
      &.incorrect
        opacity: 0.5

        &.active
          animation: rubber 600ms 1 forwards

      .btn:before,
      .btn:after
        background: transparent

      &.correct .btn
        background-color: #eee
        background-image: none

  &.game-count-down
    .selectable.fade-in
      opacity: 0
