.slide-circle-the-answer
  .circle-canvas
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%

  .circle-canvas-container
    z-index: 0

  .circle-boxes
    pointer-events: none

  .circle-box-text
    transition: all 300ms

  .circle-box
    + .circle-box
      border-top: 1px solid

    &.active
      border-top-color: transparent

      .circle-box-text
        background-color: #fff
        color: #222
        border-top-color: transparent
        border-radius: 4px

        .text-dark &
          background-color: rgba(#000 ,0.8)
          color: #fff

      + .circle-box
        border-top-color: transparent

  .slide-footer
    z-index: 100

  .multi-content-video,
  .multi-content-audio
    pointer-events: all

  &.game-count-down
    .circle-box
      color: transparent
      text-shadow: none

  &.state-complete
    .circle-box.active
      animation: pulse 900ms 600ms 1 forwards

      &.incorrect
        animation-name: rubber
        animation-duration: 600ms
        animation-delay: 0ms

        .circle-box-text
          color: inherit
          background-color: rgba(#222, 0.4)
