@use '../core' as *
@use './icons' as *

#text-input-view
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0
  z-index: -1
  border-radius: 3px
  background: #FFFFFF
  color: #000000
  text-shadow: none
  opacity: 0
  transition: all 300ms, z-index 0ms 300ms
  box-sizing: border-box

  *
    box-sizing: border-box

  &.visible
    z-index: 100
    opacity: 1
    transition: all 300ms

  &.active
    #text-input-scroll
      opacity: 1

  #text-input-container
    padding-top: constant(safe-area-inset-top)
    padding-top: env(safe-area-inset-top)
    display: flex
    flex-direction: column
    width: 100%
    height: 100%

  #text-input-scroll
    display: flex
    opacity: 0
    transition: opacity 300ms
    flex-grow: 1
    overflow: scroll
    -webkit-overflow-scrolling: touch

  #text-input-area
    display: flex
    flex-grow: 1
    border: none
    border-radius: 0
    box-shadow: none
    font-size: 18px
    resize: none

    -webkit-appearance: none

    &:focus
      outline: none

  #text-input-title,   #text-input-prompt
    margin: 0

  #text-input-container-top
    display: flex
    align-items: center
    justify-content: stretch
    z-index: 1
    min-height: 50px
    width: 100%
    border-top: 1px solid #E8EBEF
    border-bottom: 1px solid #E8EBEF
    background: #FFFFFF
    color: #9CA9B1

  #text-input-count
    display: none

  #text-input-prompt
    width: 100%
    overflow: hidden

  #text-input-dismiss-btn
    white-space: nowrap
    font-size: 1em
    flex-grow: 0

  &.has-min
    #text-input-count
      @extend %icon
      @extend .icon-circle-tick
      margin-right: 1em
      display: block
      float: right
      position: relative
      transition: color 300ms

      &:before
        margin: -0.25em -0.05em
        position: absolute
        right: 0
        color: #9CA9B1
        font-size: 2em
        opacity: 0
        transition: all 600ms $bounce-md
        transform: scale3d(0.2, 0.2, 0.2)

  &.is-complete
    #text-input-count
      color: #FFFFFF

      &:before
        opacity: 1
        transform: scale3d(1, 1, 1)

  @media (max-width: $screen-md-max)
    #text-input-dismiss-btn
      border: none
      background: transparent
      line-height: inherit
      color: #46B4E9

  @media (min-width: $screen-md-min)
    #text-input-dismiss-btn
      margin-top: -0.2em
      right: 2.4em

    &.has-min #text-input-count:after
      top: 0
      right: 1em
