@use '../../sass/core' as *

.slide-image-collection
  .title
    padding-bottom: 0.5em

  .details-parent
    position: relative
    top: -100%
    pointer-events: none

  .image-details
    position: absolute
    top: 0
    left: 0
    width: 100%
    opacity: 0
    transform: scale(0.9)

  .image-full-size
    box-sizing: border-box
    object-fit: contain

  .tappies-parent
    transition: opacity 300ms

  .tappies
    margin: 0 -0.5em
    line-height: 0

  .tappy
    box-sizing: border-box
    cursor: pointer
    overflow: hidden
    padding: 0.5em

    border: none
    background-color: transparent

    &.visited
      .tappy-child
        transition: all 300ms
        opacity: 0.6
        background: rgba(#000, 0.4)

  .thumbnail
    max-height: 100%
    margin: 0 auto

  &.details-active
    .tappies-parent
      opacity: 0
      pointer-events: none

    .details-parent
      pointer-events: all

  .image-details-visible
    z-index: 1

  @media (min-width: $screen-lg-min)
    .image-full-size
      height: auto !important
      max-height: 100%
    .content-lg-fill
      height: 100%
    .image-container
      display: flex
      justify-content: center
      align-items: center
      height: 100%
