@use '../../sass/core' as *

.slide-game-true-or-false
  .content-playing .slide-footer-prompt
    pointer-events: none !important

  .game-statement-container
    z-index: 1

  .game-statement
    margin: 0 auto
    max-width: 440px
    background-color: #fff
    background-clip: padding-box
    border: 1px solid rgba(#000, 0.1)
    border-bottom-width: 2px
    color: #333
    border-radius: 4px
    transition: opacity 300ms
    position: relative
    z-index: 10

    &.hidden
      opacity: 0

  .game-true
    .game-label
      margin-left: -1em
      padding-right: 2em

      @media (max-width: $screen-sm-max)
        transform: translate(0, -50%) rotate(90deg)

  .game-false
    .game-label
      margin-right: -1em
      padding-left: 2em

      @media (max-width: $screen-sm-max)
        padding-left: 3em
        transform: translate(0, -50%) rotate(-90deg)

  .game-statement-container,
  .game-true,
  .game-false
    float: left

  .game-true,
  .game-false
    margin: -0.5em 0
    padding: 1em 0
    box-sizing: content-box
    border-radius: 0
    transition: background-color 300ms

    .game-label
      @media (max-width: $screen-sm-max)
        white-space: pre
        width: 1000px
        margin: 0 -500px

    &.active
      background-color: rgba(#000, 0.3)
