@use '../engine/icons' as *

.mode-screenshot
  *, *:before, *:after,
  [class*=delay-],
  .slide.active .slide-up,
  .slide.active .slide-down,
  .slide.active .slide-left,
  .slide.active .slide-right,
  .slide.active .scale-up,
  .slide.active .scale-down,
  .slide.active .fade-in
    transition: none !important
    animation: none !important

  .slide .slide-up,
  .slide .slide-down,
  .slide .slide-left,
  .slide .slide-right,
  .slide .scale-up,
  .slide .scale-down,
  .slide .fade-in
    opacity: 1
    transform: translate3d(0, 0, 0) scale(1)

  ol.md-list
    list-style-type: decimal
    padding: 0 1.7em

  ol.md-list li
    padding: 0

  ol.md-list li:before
    content: none

  #lesson-header-title
    width: calc(100% - 8em)

  video
    background: rgba(#000, 0.8) !important

  .multi-content-wrapper-video
    @extend %icon
    @extend .icon-play

    &:before
      display: block
      position: absolute
      z-index: 10
      top: 50%
      left: 50%
      box-sizing: border-box
      margin: -1em
      font-size: 2em
      width: 2em
      height: 2em
      line-height: 1.8em
      border-radius: 50%
      background-color: rgba(#333, 0.7)
      border: 4px solid rgba(#fff, 0.1)
      background-clip: padding-box
