@use '../../sass/core' as *

.slide-game-jeopardy
  .content-playing .slide-footer-prompt
    pointer-events: none !important

  .game-box
    position: relative

  .game-menu,
  .game-question
    transition: opacity 300ms 300ms
    position: absolute
    width: 100%

  .game-question
    z-index: 1
    opacity: 0
    pointer-events: none

  .game-category-title
    height: 6em

    @media (min-width: $screen-sm-min)
      height: 2.4em

  .question-answered
    pointer-events: none

    .block-dk
      transition: all 300ms
      color: #222
      background-color: #fff

  &.show-question
    .game-menu
      opacity: 0

    .game-question
      opacity: 1
      pointer-events: all
