.slide-expandable-list
  .item-middle .item-button
    border-radius: 0

  .item-first .item-button
    border-radius: 4px 4px 0 0

  .item-last
    .item-button,
    .item-content
      border-radius: 0 0 4px 4px

  .item-button
    display: block
    padding-right: 2.5em
    transition: background-color 300ms
    position: relative
    margin: 0 0 -2px
    z-index: 1

    ul, ol
      padding: 0 1em

    &:after
      content: "\F12E"
      font-family: icons
      color: #ccc
      font-weight: normal
      font-size: 1em
      height: 1em
      position: absolute
      right: 0.75em
      top: 50%
      margin-top: -0.5em
      text-shadow: none

  .item-content
    border-radius: 0
    transform-origin: top center
    overflow: hidden
    opacity: 0
    position: absolute
    top: 100%
    left: 0
    right: 0
    transform: translate3d(0, 0, 0) scale(1, 0)
    transition: inherit

  .item
    position: relative

    &.active
      .item-content
        transform: translate3d(0, 0, 0) scale(1, 1)
        opacity: 1

      .item-button
        background-color: #e5e5e5
        border-bottom-left-radius: 0
        border-bottom-right-radius: 0

        &:after
          content: "\F12F"
          color: #aaa
