@use '../../sass/core' as *
@use '../../sass/components/typography' as *
@use '../../sass/components/content' as *

.slide-drag-to-match
  .drag-to-match-outer
    display: flex
    flex-direction: column

  .drag-to-match-inner
    display: flex
    min-height: 0
    height: 100%

  .draggy-container, .droppy-container
    width: 50%
    padding: 0.9em

  .draggy-container
    z-index: 5
    position: relative

  .multi-content-wrapper,
  .multi-content-media
    background: transparent
    height: 100%
    padding: 0

  .multi-content-image
    max-height: 100%

  .draggy-img,
  .droppy-zone
    position: relative

  .draggy,
  .droppy,
  .droppy-zone
    width: 100%
    height: 100%

  .draggies .block-dk,
  .draggies,
  .droppies
    transition: all 300ms

  .draggy
    transition: transform 300ms

    &.active
      position: relative

    &.fade
      opacity: 0
      pointer-events: none

  .draggy-img
    margin: auto
    max-height: 100%
    max-width: 100%
    min-width: 60px
    min-height: 60px

  .droppy
    transition: background-color 300ms
    position: relative

    &.contains
      .multi-content
        opacity: 0

    &.incorrect
      animation: rubber 600ms 1 forwards

    &.active
      background: rgba(0, 0, 0, 0.3)

  .multi-content
    transition: transform 300ms, opacity 300ms
    @extend %content-ver

  .multi-content-text
    font-size: 1em
    line-height: 1
    @extend .text-bold
    @extend .text-md

  .short-text .multi-content-text
    @extend .text-uppercase

  .droppy-zone
    position: absolute
    top: 0
    left: 0

  .draggy-container .drag-to-match-item
    float: right
  .droppy-container .drag-to-match-item
    float: left

  .drag-to-match-item
    width: 100%
    text-align: center

  @media (max-width: $screen-md-max)
    .droppies,
    .draggies
      text-align: center

  @media (min-width: $screen-sm-min)
    .drag-to-match-item
      width: 50%
      height: 50% !important
      padding: 12px

  &.state-touched,
  &.state-complete
    .draggies .block-dk
      background-color: rgba(#000, 0.1)

    .draggy-container,
    .droppy-container
      transform: translate3d(-50%, 0, 0)

  &.state-complete
    .draggies .block-dk
      background-color: transparent

    .multi-content-wrapper
      animation: pulse 900ms 600ms 1 forwards

    .incorrect .multi-content-wrapper
      animation: rubber 600ms 1 forwards

    .droppy.correct
      background-color: rgba(0, 0, 0, 0.3)
