.slide-multiple-choice-survey
  .col
    display: flex
    flex-direction: column
    justify-content: center
    height: 100%
    
  &.image-focus-true
    .multi-content-wrapper-image:after
      content: "\f108"
      font-family: icons
      font-size: 1.6em
      line-height: 1
      position: absolute
      top: 4px
      right: 4px
      width: 1em
      height: 1em
      color: #fff
      background: rgba(#000, 0.12)
      border-radius: 4px

  &.state-complete
    .selectable
      &.incorrect
        opacity: 0.5

        &.active
          animation: rubber 600ms 1 forwards

      .btn:before,
      .btn:after
        background: transparent

      &.correct .btn
        background-color: #eee
        background-image: none
