@use '../engine/icons' as *

.video-controls
  line-height: 0
  transition: opacity 300ms
  text-align: center
  opacity: 0
  width: 100%
  pointer-events: none
  position: absolute
  z-index: 10
  color: #fff

  .video-paused &
    opacity: 1
    pointer-events: all

.video-error-msg
  text-align: center
  opacity: 0
  width: 100%
  pointer-events: none
  position: absolute
  z-index: 10
  line-height: 1.4

  .video-error &
    opacity: 1

.video-error,
.video-paused
  .slide-loader
    opacity: 0

.slide.video-loading
  .slide-loader
    opacity: 1
    z-index: 100

.video-play,
.video-replay
  width: 8em
  height: 8em
  font-size: 1rem
  line-height: 8em
  margin: 1em
  border-radius: 50%
  box-sizing: border-box
  border: 4px solid rgba(#fff, 0.1)
  background-color: rgba(#333, 0.7)
  color: inherit
  background-clip: padding-box
  display: inline-block
  transition: background-color 300ms
  -webkit-backdrop-filter: blur(4px)
  @extend %icon

  &:before
    font-size: 4em
    margin-top: -4px
    display: inline-block

  &.__active
    background-color: rgba(#111, 0.9)

.video-play
  @extend .icon-play

.video-replay
  @extend .icon-replay
