.slide-media-collection
  .title
    padding-bottom: 0.5em

  .play-button
    background-color: rgba(51, 51, 51, 0.7)
    border-radius: 50%
    height: 4rem
    width: 4rem
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)

    .arrow
      position: absolute
      top: 50%
      left: 53%
      transform: translate(-47%, -50%)
      width: 0
      height: 0
      border-top: 1rem solid transparent
      border-bottom: 1rem solid transparent
      border-left: 1.4rem solid white

  .video-js
    font-size: 12px
    background: transparent
    max-width: 100%
    height: inherit !important

    .vjs-control-bar
      width: auto
      left: -4px
      right: -4px
      bottom: -4px

    &.vjs-fullscreen
      video
        max-height: none

  .content-container
    position: relative

  .parent-container
    position: relative

  .details-parent
    width: 100%
    position: absolute
    top: 0
    left: 0
    pointer-events: none

  .details-overlay
    position: absolute
    top: 0
    left: 0

  .details
    position: absolute
    opacity: 0
    width: 100%
    max-height: 100%
    top: 50%
    left: 50%
    transform: translate3d(-50%, -50%, 0)
    transition: all 300ms ease-in-out

    .caption
      padding: 1rem 0

  .full-size
    box-sizing: border-box
    object-fit: contain

  .tappies-parent
    transition: opacity 300ms

  .tappies
    margin: 0 -0.5em
    line-height: 0

  .tappy
    box-sizing: border-box
    cursor: pointer
    overflow: hidden
    padding: 0.5em

    .tappy-child
      position: relative

      .tappy-overlay
        opacity: 0
        position: absolute
        top: 0
        left: 0
        width: 100%

    &.visited
      .tappy-child
        transition: all 300ms
        opacity: 0.6
        background: rgba(#000, 0.4)

  .thumbnail
    padding: 0 !important

  audio.thumbnail
    position: absolute
    width: 90%
    top: 50%
    left: 5%
    transform: translate3d(0, -50%, 0)

  .thumbnail,
  .full-size
    max-width: 100%
    max-height: 100%

  &.details-active
    .tappies-parent
      opacity: 0
      pointer-events: none

    .details-parent
      pointer-events: all
