=font-face($name, $family, $weight, $style)
  $path: "assets/font/#{$name}"
  @font-face
    font-family: $family
    src: url("#{$path}.woff") format("woff")
    font-weight: $weight
    font-style: $style

+font-face("NotoSans-Regular", "Noto Sans", 400, normal)
+font-face("NotoSans-Italic", "Noto Sans", 400, italic)
+font-face("NotoSans-SemiBold", "Noto Sans", 600, normal)
