@use '../slider/style' as *

.slide-slider-survey
  @extend .slide-slider

  .slider-label-text
    position: absolute
    top: 100%
    left: 0
    right: 0
    margin: -12px

  .slider-label-min,
  .slider-label-max
    position: relative
    line-height: 1em
    padding: 5px 6px 4px
    background: rgba(0, 0, 0, 0.3)
    border-radius: 2px
    min-width: 12px
    max-width: 120px
    opacity: 0.5

    &:after
      content: ""
      position: absolute
      border: 4px solid transparent
      border-bottom-color: rgba(0, 0, 0, 0.3)
      margin: -4px
      top: -4px

  .slider-label-min:after
    left: 12px

  .slider-label-max:after
    right: 12px
